<template>
  <div class="ChatInput">
    <div class="header">
      <inputTab>
        <!-- 图片 -->
        <SendImage
          v-if="askMode !== 'ROUND'"
          slot="image"
          class="float-left"
          :has-server="ifHasServer('fd_ask')"
          :submit-btn-status-disabled="submitBtnStatusDisabled"
        />
        <!-- 视频 -->
        <SendVideo
          v-if="askMode !== 'ROUND'"
          slot="video"
          class="float-left"
          :has-server="ifHasServer('fd_ask')"
          :submit-btn-status-disabled="submitBtnStatusDisabled"
        />
        <!-- 快捷回复 -->
        <FastReply
          slot="fastReply"
          class="float-left"
          @ClickFastReply="getFastReply"
        />
      </inputTab>
    </div>
    <!-- 回合制输入框 -->
    <Bout
      v-if="askMode === 'ROUND' && !showVoice"
      ref="boutInput"
      :closeBtnStatus="
        closeConsultBtnStatus ||
          (getReplaceStatus.replace &&
            getReplaceStatus.replace.status === 'replace_by_other')
      "
      @closeBoutConsult="closeConsult"
    ></Bout>
    <div class="chat-input-box">
      <div v-if="showVoice">
        <PhoneInput
          class="chat-phone-main"
          :show-voice="lastStatus"
          :last-ask-id="lastAsk._id"
          :time-cha="timeCha"
          :hour="timeCha.hours"
          :minute="timeCha.minutes"
          :second="timeCha.seconds"
          @doctorStartTransfer="doctorStart"
          @doctorCloseTransfer="doctorClose"
        />
      </div>
      <!--start askInput -->
      <div v-if="askMode === 'IM'" class="chat-input-main">
        <div class="body">
          <div class="input-wrapper">
            <textarea
              v-if="submitCommond === 'enter'"
              id="messgae_input"
              v-model="currentMessage"
              class="border-none"
              type="textarea"
              autofocus
              rows="7"
              :readonly="loading"
              placeholder="请输入内容，内容不能为空"
              @keydown.enter.exact="keyEnter($event)"
              @keydown.ctrl.enter="lineFeed($event)"
            />
            <textarea
              v-if="submitCommond === 'ctrl'"
              id="messgae_input"
              v-model="currentMessage"
              class="border-none"
              type="textarea"
              autofocus
              rows="7"
              :readonly="loading"
              placeholder="请输入内容，内容不能为空"
              @keydown.ctrl.enter.exact="keyCtrlEnter($event)"
              @keydown.enter.exact="lineFeed($event, 'enter')"
            />
          </div>
        </div>
        <div class="footer">
          <!--
          submitBtnDisabled={{ submitBtnDisabled }} submitBtnStatusDisabled={{
            submitBtnStatusDisabled
          }}
          haveMessage={{ haveMessage }}
        -->
          <div class="btn-wrapper">
            <el-button
              class="btn"
              size="mini"
              plain
              type="primary"
              :disabled="
                closeConsultBtnStatus ||
                  (getReplaceStatus.replace &&
                    getReplaceStatus.replace.status === 'replace_by_other')
              "
              @click="closeConsult"
              >结束会话</el-button
            >
            <el-dropdown
              style="border:1px solid #409EFF;border-radius:4px;"
              class="drop-down"
              split-button
              type="primary"
              size="mini"
              @command="submitCommondFun"
            >
              <span
                v-if="submitBtnDisabled"
                class="submitBtnDisabledTrue"
                @click="submitDisabled"
                >发送</span
              >
              <span
                v-if="!submitBtnDisabled"
                id="sendBtn"
                :class="
                  !haveMessage
                    ? 'submitBtnDisabledTrue'
                    : 'submitBtnDisabledFalse'
                "
                @click="submitNewMsg"
                >发送</span
              >
              <el-dropdown-menu slot="dropdown" size="mini">
                <el-dropdown-item command="ctrl">
                  <i
                    v-show="submitCommond === 'ctrl'"
                    class="el-icon-check"
                  /><span :class="submitCommond != 'ctrl' ? 'marginleft16' : ''"
                    >Ctrl+Enter发送
                  </span>
                </el-dropdown-item>
                <el-dropdown-item actived command="enter"
                  ><i
                    v-show="submitCommond === 'enter'"
                    class="el-icon-check"
                  /><span
                    :class="submitCommond != 'enter' ? 'marginleft18' : ''"
                    >Enter发送
                  </span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :title="noSuggest ? '请填写建议或叮嘱' : '即将结束本次' + chatType"
      :visible.sync="consultDialog.dialogVisible"
      width="23%"
      center
      :before-close="handleClose"
    >
      <div v-if="!noSuggest">
        <div class="suggest">
          <p>
            状态：<span>{{ suggestStatus }}</span>
          </p>
          <p>
            {{ suggestType(suggestion_type) }}：<span>{{
              suggestContent
            }}</span>
          </p>
        </div>
        <div class="tip">
          <i class="el-icon-info" />
          建议（或叮嘱）部分的内容会反馈给用户，请谨慎填写，如需变更，请回到当前咨询轨迹区域进行编辑
        </div>
      </div>
      <div v-if="noSuggest" class="suggest-info">
        请在当前咨询轨迹区域填写建议，该建议（或叮嘱）将作为此次咨询的指导方案提交给用户，请谨慎填写！
      </div>
      <div v-if="chatType === '转诊' && !noSuggest" class="transfer-tip">
        另外，请确认本次转诊是否有效？<br />
        如确认无联系不上、意外断线等异常情况，请选择有效转诊，该用户转诊次数将自动-1；<br />
        如有异常，请选择无效转诊按钮，将不会减少用户电话转诊次数
      </div>
      <span v-if="chatType === '咨询'" slot="footer" class="dialog-footer">
        <el-button v-if="!noSuggest" @click="handleClose"
          >&nbsp; 取消 &nbsp;
        </el-button>
        <el-button type="primary" @click="confirmCloseConsulte">{{
          noSuggest ? '返回填写' : '确认结束'
        }}</el-button>
      </span>
      <span v-if="chatType === '转诊'" slot="footer" class="dialog-footer">
        <el-button v-if="!noSuggest" @click="confirmCloseConsulte(false)"
          >无效转诊</el-button
        >
        <el-button type="primary" @click="confirmCloseConsulte(true)">{{
          noSuggest ? '返回填写' : '有效转诊'
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {
  handleMessageSend,
  closeConsultApi,
  changeUserHabit
} from '@/api/message'
import { closeBoutConsultApi } from '@/api/bout'
import { doctorCloseTransfer } from '@/api/diagnose'
import { mapGetters, mapActions } from 'vuex'

import SendImage from './components/SendImage'
import SendVideo from './components/SendVideo'
import FastReply from './components/FastReply'
import inputTab from './inputTab'

import PhoneInput from './components/PhoneInput'
import Bout from './bout'

import { ifHasServer } from '@/utils/chat/index'
import { msgIsLink } from '@/utils/index.js'
import { suggestType } from '@/utils/dictionary'
import { waitTime } from '@/utils/moment'
export default {
  name: 'ChatInput',
  components: {
    SendImage,
    SendVideo,
    PhoneInput,
    FastReply,
    inputTab,
    Bout
  },
  data() {
    return {
      time1: '',
      submitCommond: 'enter',
      currentMessage: '',
      chatType: '咨询',
      closeType: '', //判断结束会话的类型是否回合制
      closeConsultBtnStatus: true,
      noSuggest: false, // store中存储的医生建议是否为空,true表示为空
      suggestStatus: '未解决',
      suggestion_type: 'suggestion',
      suggestContent: '',
      submitBtnDisabled: true,
      submitBtnStatusDisabled: true,
      haveMessage: false,
      loading: false,
      lastStatus: false,
      timeCha: {
        hours: '',
        minutes: '',
        seconds: ''
      },
      showVoice: false,
      consultDialog: {
        title: '即将结束本次咨询',
        dialogVisible: false
      },
      babyId: this.$route.query.id
    }
  },
  computed: {
    ...mapGetters([
      'currentTextAnswer', // 图文咨询
      'currentVoiceAnswer', // 当前咨询的电话问诊回答
      'currentTransferTextAnswer', // 图文转诊回答
      'habit',
      'currentBabyList',
      'activedBabyId',
      'userInfo',
      'activedBaby',
      'lastAsk',
      'getReplaceStatus',
      'askMode',
      'role'
    ])
  },
  watch: {
    currentMessage(val) {
      if (val) {
        this.setSubmitBtnDisabled()
        this.getSubmitBtnIsDisabled()
      }
      if (val && !this.submitBtnStatusDisabled) {
        this.haveMessage = true
      } else {
        this.haveMessage = false
      }
    },
    habit(val) {
      this.submitCommond = this.habit.submitCommond
        ? this.habit.submitCommond
        : 'ctrl'
    },
    lastAsk: {
      handler: function(val, oldval) {
        this.initPage()
      },
      deep: true
    },
    currentTextAnswer() {
      this.showSuggestDialog()
    },
    currentVoiceAnswer() {
      this.showSuggestDialog()
    },
    currentTransferTextAnswer() {
      this.showSuggestDialog()
    },
    activedBabyId(newVal, oldVal) {
      // 切换宝宝时，将结束会话按钮重置为禁用状态
      this.closeConsultBtnStatus = true
      // 切换宝宝时，缓存消息输入框内容
      if (oldVal) {
        const params = {
          babyId: oldVal,
          msg: this.currentMessage
        }
        window.sessionStorage.setItem(
          'localMsg' + oldVal,
          JSON.stringify(params)
        )
      }
      const localMsg = window.sessionStorage.getItem('localMsg' + newVal)
      const localMsgJson = JSON.parse(localMsg)
      if (localMsgJson && localMsgJson.babyId === newVal) {
        this.currentMessage = localMsgJson.msg
      } else {
        this.currentMessage = ''
      }
      // 切换用户时，获取用户的快捷键
      this.submitCommond = this.habit.submitCommond
        ? this.habit.submitCommond
        : 'ctrl'
    }
  },
  created() {},
  methods: {
    ifHasServer,
    ...mapActions({
      getUserInfo: 'user/getInfo'
    }),
    initPage() {
      if (this.lastAsk && Object.keys(this.lastAsk).length > 0) {
        console.log('this.lastAsk', this.lastAsk)
        this.closeBtnIsDisabled()
        this.showVoiceFun()
        this.setSubmitBtnDisabled()
        this.getSubmitBtnIsDisabled()

        clearInterval(this.time1)
        this.setTimeCount()
      }
    },
    // 换行
    lineFeed(event, ctrl) {
      if (ctrl === 'enter') {
        return
      }
      event.preventDefault()
      this.currentMessage = this.currentMessage + '\n'
    },
    setSubmitBtnDisabled() {
      if (this.activedBaby.is_asking) {
        this.submitBtnDisabled = false
      } else {
        this.submitBtnDisabled = true
      }
    },
    getFastReply(val) {
      if (this.askMode === 'IM') {
        this.currentMessage += val
        return
      }
      const answerIndex = this.$refs.boutInput.answerIndex || 0
      this.$refs.boutInput.answerList[answerIndex].content += val
    },
    showSuggestDialog() {
      this.chatType = '咨询'
      let val = this.currentTextAnswer
      if (
        this.lastAsk &&
        this.lastAsk.transfer_wait_user_at &&
        this.lastAsk.has_transfer &&
        this.lastAsk.transfer_type === 'doctor_voice'
      ) {
        val = this.currentVoiceAnswer
        this.chatType = '转诊'
      } else if (
        this.lastAsk &&
        this.lastAsk.transfer_wait_user_at &&
        this.lastAsk.has_transfer &&
        this.lastAsk.transfer_type === 'doctor_text'
      ) {
        val = this.currentTransferTextAnswer // 医生图文
        this.chatType = '咨询'
      } else if (
        this.lastAsk &&
        this.lastAsk.has_transfer &&
        this.lastAsk.transfer_type === 'dietician_text'
      ) {
        val = this.currentTransferTextAnswer // 营养师图文
        this.chatType = '咨询'
      }
      if (val) {
        this.suggestStatus = val.status === 'resolved' ? '已解决' : '未解决'
        this.suggestion_type = val.suggestion_type
        this.suggestContent = val.suggestion ? val.suggestion : ''
        if (!this.suggestStatus || !this.suggestContent) {
          this.noSuggest = true // 测试弹窗
        } else {
          this.noSuggest = false
        }
      } else {
        this.noSuggest = true
      }
    },
    setTimeCount() {
      this.timeCha = this.waitTime(this.lastAsk.transfer_asking_at, 'obj')
      this.time1 = setInterval(() => {
        this.timeCha = this.waitTime(this.lastAsk.transfer_asking_at, 'obj')
      }, 1000)
    },
    waitTime,
    suggestType,
    showVoiceFun() {
      if (
        this.lastAsk.family_member === this.activedBabyId &&
        this.lastAsk.has_transfer &&
        this.lastAsk.status !== 'ask_end' &&
        (this.lastAsk.transfer_status === 'start' ||
          this.lastAsk.transfer_status === 'asking') &&
        this.lastAsk.transfer_type === 'doctor_voice' &&
        this.lastAsk.transfer_role === this.role
      ) {
        this.showVoice = true
        if (this.lastAsk.transfer_status === 'asking') {
          this.lastStatus = true
        } else {
          this.lastStatus = false
        }
      } else {
        this.showVoice = false
      }
    },
    doctorStart() {
      this.chatType = '转诊'
    },
    doctorClose() {
      this.closeConsult()
    },
    closeBtnIsDisabled() {
      if (this.activedBaby.is_asking) {
        if (
          this.lastAsk &&
          this.lastAsk.status === 'asking' &&
          !this.lastAsk.has_transfer &&
          this.role === 'hm'
        ) {
          // 咨询开始，转诊未开始
          this.closeConsultBtnStatus = false
        } else if (this.lastAsk && this.lastAsk.status === 'pending_close') {
          // 待用户确认结束会话时，结束会话按钮不可用
          this.closeConsultBtnStatus = true
        } else if (
          this.lastAsk &&
          this.lastAsk.status === 'asking' &&
          this.lastAsk.has_transfer
        ) {
          // 咨询开始，转诊开始 disabled:false
          if (
            this.role === this.lastAsk.transfer_role &&
            (this.lastAsk.transfer_status === 'start' ||
              this.lastAsk.transfer_status === 'asking')
          ) {
            this.closeConsultBtnStatus = false
          } else if (
            this.transfer_role !== this.role &&
            this.lastAsk.transfer_status !== 'start' &&
            this.lastAsk.transfer_status !== 'wait_user' &&
            this.lastAsk.transfer_status !== 'asking' &&
            this.role === 'hm'
          ) {
            // 咨询开始转诊开始，转诊id和当前用户id一致
            this.closeConsultBtnStatus = false
          } else if (
            this.lastAsk.transfer_status === 'apply' &&
            this.role === 'hm'
          ) {
            // 咨询开始 转诊未成功，且当前用户为健康管理师时
            this.closeConsultBtnStatus = false
          } else {
            this.closeConsultBtnStatus = true // true 按钮禁用
          }
        } else {
          // 不在咨询中
          this.closeConsultBtnStatus = true
        }
      } else {
        this.closeConsultBtnStatus = true
      }
    },
    getSubmitBtnIsDisabled() {
      // 根据聊天状态判断发送按钮是否可用
      if (
        this.lastAsk &&
        (this.lastAsk.status === 'asking' ||
          this.lastAsk.status === 'pending_ask' ||
          this.lastAsk.status === 'pending_close') &&
        this.role === 'hm'
      ) {
        // 咨询开始，转诊未开始
        this.submitBtnStatusDisabled = false
      } else if (
        this.lastAsk &&
        this.lastAsk.status === 'asking' &&
        this.lastAsk.has_transfer
      ) {
        // 咨询开始，转诊开始
        if (
          (this.lastAsk.transfer_status === 'start' ||
            this.lastAsk.transfer_status === 'asking') &&
          this.role === this.lastAsk.transfer_role
        ) {
          //  营养师\医生图文
          this.submitBtnStatusDisabled = false
        } else if (this.role === 'hm') {
          // 只要会话开始，则hm在会话的整个过程中一直拥有发送消息的权限
          this.submitBtnStatusDisabled = false
        } else {
          this.submitBtnStatusDisabled = true
        }
      }
    },
    keyEnter(event) {
      this.closeBtnIsDisabled()
      this.setSubmitBtnDisabled()
      this.getSubmitBtnIsDisabled()
      if (
        this.submitCommond === 'enter' &&
        !this.submitBtnDisabled &&
        !this.loading &&
        this.haveMessage
      ) {
        this.loading = true
        this.submitNewMsg()
        event.preventDefault()
      }
    },
    keyCtrlEnter(event) {
      this.closeBtnIsDisabled()
      this.setSubmitBtnDisabled()
      this.getSubmitBtnIsDisabled()
      if (
        this.submitCommond === 'ctrl' &&
        !this.submitBtnDisabled &&
        !this.loading &&
        this.haveMessage
      ) {
        this.loading = true
        this.submitNewMsg()
        event.preventDefault()
      }
    },
    msgIsLink,
    handleClose() {
      this.consultDialog.dialogVisible = false
      this.showSuggestDialog()
    },
    confirmCloseConsulte(flag) {
      if (this.noSuggest) {
        this.handleClose()
        return
      } else {
        if (this.chatType === '咨询') {
          // 确认结束会话接口-回合制
          if (this.closeType === 'bout') {
            this.closeBoutConsult()
          } else {
            // 即时通讯类型
            this.confirmCloseConsult()
          }
        } else if (this.chatType === '转诊') {
          this.endDoctorVoiceApi(flag)
        }
      }
    },
    closeBoutConsult() {
      closeBoutConsultApi(this.lastAsk._id).then(res => {
        if (res.data.result) {
          this.$message({
            type: res.data.result ? 'success' : 'error',
            message: res.data.message
          })
          this.handleClose()
        }
      })
    },
    endDoctorVoiceApi(flag) {
      doctorCloseTransfer(this.lastAsk._id, {
        is_transfer_valid: flag
      }).then(res => {
        if (res && res.data.result) {
          const validTip = '电话转诊结束，转诊次数扣除成功'
          const invalidTip = '电话转诊结束，减免转诊次数成功'
          this.$message({
            type: 'success',
            message: flag ? validTip : invalidTip
          })
          this.handleClose()
          this.$store.dispatch('chat/getCustomerTree')
        } else {
          const validTip = '电话转诊结束失败，转诊扣除操作失败'
          const invalidTip = '电话转诊结束失败，减免转诊次数失败'
          this.$message({
            type: 'error',
            message: flag ? validTip : invalidTip
          })
        }
      })
    },
    confirmCloseConsult() {
      closeConsultApi(this.lastAsk._id).then(res => {
        const operatorName = '结束会话'
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result
            ? operatorName + '请求成功，待用户确认'
            : operatorName + '请求失败，待用户确认'
        })
        this.handleClose()
      })
    },
    submitCommondFun(val) {
      if (val) this.submitCommond = val
      const params = {
        habit_key: 'submitCommond',
        habit_value: this.submitCommond
      }
      changeUserHabit(params).then(res => {
        this.$message({
          type: res && res.data.result ? 'success' : 'error',
          message: res.message
        })
        this.getUserInfo()
      })
    },
    submitDisabled() {
      if (!this.activedBaby.is_asking) {
        this.$message({
          type: 'warning',
          message: '当前用户未在咨询中，您不能主动向用户发起对话'
        })
      }
    },
    async submitNewMsg(val) {
      if (this.submitBtnStatusDisabled) {
        return
      }
      this.submitBtnDisabled = true
      const mystr = this.currentMessage.replace(/(^\s*)|(\s*$)/g, '') // 去除首尾空格
      if (!this.currentMessage || mystr.length <= 0) {
        this.$message.error('请输入内容后发送')
        this.currentMessage = ''
        this.loading = false
        return
      }
      if (!this.lastAsk._id) {
        this.$message.error('当前对话的用户fd_ask为null,请联系相关人员处理')
        return
      }
      const currentFdAsk = this.lastAsk._id
      window.sessionStorage.setItem('currentFdAsk', currentFdAsk)
      const msgType = this.msgIsLink(this.currentMessage) ? 'link' : 'text'
      const params = {
        fd_ask: sessionStorage.getItem('currentFdAsk'), // 咨询id,
        message_body: {
          type: msgType,
          content: this.currentMessage
        }
      }
      await handleMessageSend(params)
        .then(res => {
          this.loading = false
          this.submitBtnDisabled = false
          this.currentMessage = ' '
          if (res.data.result) {
            window.sessionStorage.removeItem('currentFdAsk')
            this.$store.dispatch('setBottomBtn', false, { root: true }) // 隐藏直达最新消息按钮
            this.$store.dispatch('pushCurrentIMInfoMessages', res.data.message)
          } else {
            window.sessionStorage.removeItem('currentFdAsk')
            this.$message.error(res.data.message)
          }
        })
        .catch(error => {
          this.loading = false
          this.submitBtnDisabled = false
        })
    },
    closeConsult(val) {
      if (val === 'bout') {
        this.closeType = 'bout'
      } else {
        this.closeType = 'im'
      }
      this.showSuggestDialog()
      this.consultDialog.dialogVisible = true
    }
  }
}
</script>
<style lang="scss" scoped>
.ChatInput {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 134px;
  border-right: 1px solid #eee;
  .chat-input-box {
    position: relative;
    width: 100%;
    height: 99%;
  }
  .chat-phone-main {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1000;
  }
  .chat-input-main {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .submitBtnDisabledTrue {
    display: inline-block;
    padding: 7px 12px 7px;
    cursor: not-allowed;
    .el-button-group {
      button,
      .el-button {
        cursor: not-allowed;
      }
    }
  }
  .submitBtnDisabledFalse {
    display: inline-block;
    padding: 7px 12px 7px;
    cursor: pointer;
    .el-button-group {
      button,
      .el-button {
        cursor: pointer;
      }
    }
  }
  .header {
    position: relative;
    height: 30px;
    border-bottom: 1px solid #eee;
  }
  .body {
    position: relative;
    display: flex;
    height: 72%;
    flex-grow: 1;
    .input-wrapper {
      position: relative;
      width: 100%;
      line-height: 18px;
      textarea {
        position: absolute;
        top: 0;
        right: 0;
