<template>
  <div>
    <span class="ask-status">
      <span v-if="currentSession.chat_status === 'closed'">
        已结束
      </span>
      <span v-else>
        {{ getStatusText(currentSession.last_message.content) }}
      </span>
    </span>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data() {
    return {}
  },
  computed: {
    ...mapGetters(['currentSession'])
  },
  mounted() {},
  methods: {
    getStatusText(val) {
      const valFormat = val ? val.split('[')[1].split(']')[0] : ''
      if (val) {
        return valFormat
      } else {
        return ''
      }
    }
  }
}
</script>
<style lang="scss">
.ask-status {
  margin-left: 10px;
  padding: 4px 14px;
  line-height: 38px;
  color: #576277;
  background: #f6f7fb;
}
</style>
