<template>
  <div class="chatlist_draw">
    <scroll-box :box-height="740" style="background:#fff;">
      <div
        v-for="(customer, index) in listType === 'waiting'
          ? waitingCustomerList
          : role === 'hm'
          ? askingCustomerList
          : transferCustomerList"
        :key="index"
      >
        <session-card :session="customer" />
      </div>
    </scroll-box>
  </div>
</template>

<script>
import ScrollBox from '@/components/ScrollBox'
import { mapGetters } from 'vuex'
import SessionCard from './SessionCard'

export default {
  name: 'CustomerInDrawer',
  components: {
    ScrollBox,
    SessionCard
  },
  props: {
    listType: {
      type: String,
      require: true,
      default: ''
    }
  },
  computed: {
    ...mapGetters([
      'waitingCustomerList',
      'askingCustomerList',
      'role',
      'transferCustomerList'
    ])
  },
  methods: {}
}
</script>
