<template>
  <div class="MsgHistory">
    <el-tooltip content="聊天记录" placement="top">
      <svg-icon icon-class="liaotianjilu" @click="handleModle" />
    </el-tooltip>
    <el-dialog
      :title="dialogTitle + '的聊天记录'"
      :visible.sync="dialogVisible"
      :fullscreen="isfullscreen"
      :before-close="handleClose"
      width="863px"
    >
      <div slot="title" class="dialog-title">
        <el-row :gutter="24">
          <el-col :span="20" class="title">{{
            dialogTitle + '的聊天记录'
          }}</el-col>
          <el-col :span="4">
            <button
              type="button"
              aria-label="Close"
              class="dialog__headerbtn_shrink"
              @click="closeFull"
            >
              <i class="el-icon-minus" />
            </button>
            <button
              type="button"
              aria-label="Close"
              class="dialog__headerbtn_full"
              @click="changeFull"
            >
              <i class="el-icon-full-screen" />
            </button>
          </el-col>
        </el-row>
      </div>
      <div class="body" :class="isfullscreen === false ? 'bodyheight' : ''">
        <div class="search-box">
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-row :gutter="24">
              <el-col :span="9">
                <el-form-item label="关键词搜索">
                  <el-input
                    v-model="formInline.keyword"
                    size="mini"
                    placeholder="请输入关键词搜"
                    @input="keywordChange"
                  />
                </el-form-item>
              </el-col>
              <el-col :span="15">
                <el-form-item label="咨询开始时间">
                  <el-date-picker
                    v-model="formInline.consultationTime"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    type="datetimerange"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :default-time="['00:00:00', '23:59:59']"
                    size="small"
                    clearable
                  />
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <div class="tab-box">
          <el-tabs v-model="activeName" @tab-click="tabChange">
            <el-tab-pane label="全部" name="all">
              <p
                v-if="
                  !formInline.keyword &&
                    (!formInline.consultationTime ||
                      formInline.consultationTime.length === 0) &&
                    msgList.length === 0
                "
                class="color-999"
              >
                请输入搜索内容
              </p>
              <MsgCard
                :msg-list="msgList"
                :form-inline="formInline"
                :page="page"
                :keyword="formInline.keyword"
                :page-total="pageTotal"
                :search-type="activeName"
                @page="getPage"
                @toMessage="handleCloseDialog"
                @getMsgListMore="getMsgListMore"
              />
            </el-tab-pane>
            <el-tab-pane label="链接" name="link">
              <p
                v-if="
                  !formInline.keyword &&
                    (!formInline.consultationTime ||
                      formInline.consultationTime.length === 0) &&
                    msgList.length === 0
                "
                class="color-999"
              >
                请输入搜索内容
              </p>
              <MsgCard
                :msg-list="msgList"
                :form-inline="formInline"
                :page="page"
                :keyword="formInline.keyword"
                :page-total="pageTotal"
                :search-type="activeName"
                @page="getPage"
                @toMessage="handleCloseDialog"
                @getMsgListMore="getMsgListMore"
              />
            </el-tab-pane>
            <el-tab-pane label="图片与视频" name="imgAndVideo">
              <p
                v-if="
                  !formInline.keyword &&
                    (!formInline.consultationTime ||
                      formInline.consultationTime.length === 0) &&
                    msgList.length === 0
                "
                class="color-999"
              >
                暂无数据
              </p>
              <MsgCard
                :msg-list="msgList"
                :form-inline="formInline"
                :page="page"
                :keyword="formInline.keyword"
                :page-total="pageTotal"
                :search-type="activeName"
                @page="getPage"
                @toMessage="handleCloseDialog"
                @getMsgListMore="getMsgListMore"
              />
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import MsgCard from './MsgCard'
import { SearchApi } from '@/api/msgSearch'
export default {
  name: 'MsgHistory',
  components: {
    MsgCard
  },
  data() {
    return {
      dialogVisible: false,
      dialogTitle: '',
      isfullscreen: false,
      formInline: {
        keyword: '',
        consultationTime: []
      },
      activeName: 'all',
      timeout: null,
      msgList: [],
      // 当前页数
      page: 1,
      // 总页数
      pageTotal: 0,
      pagesize: 20
    }
  },
  computed: {
    ...mapGetters(['activedBabyId', 'currentSession'])
  },
  watch: {
    activedBabyId() {
      if (!this.dialogVisible) {
        this.activeName = 'all'
        this.titleFormat()
      }
    },
    'formInline.consultationTime'(val) {
      this.keywordChange()
    }
  },
  methods: {
    handleCloseDialog(val) {
      this.handleClose()
    },
    titleFormat() {
      const customer = this.currentSession
      const customerUser = customer.family_member
      this.dialogTitle =
        customerUser && customerUser.name ? customerUser.name : ''
      this.formInline = {
        keyword: '',
        consultationTime: []
      }
    },
    changeFull() {
      this.isfullscreen = true
    },
    closeFull() {
      this.isfullscreen = false
    },
    handleModle() {
      this.dialogVisible = true
      this.getMsgList()
    },
    handleClose() {
      this.dialogVisible = false
      this.page = 1
      this.pageTotal = 0
      this.msgList = []
    },
    tabChange() {
      this.formInline.keyword = ''
      this.formInline.consultationTime = []
      this.page = 1
      this.pageTotal = 0
      this.msgList = []
    },
    async getMsgList() {
      if (!this.activedBabyId) {
        return
      }
      if (
        !this.formInline.keyword &&
        this.activeName !== 'imgAndVideo' &&
        (!this.formInline.consultationTime ||
          this.formInline.consultationTime.length === 0)
      ) {
        return
      }
      const params = {
        family_member: this.activedBabyId,
        search_key: this.formInline.keyword,
        current_page: this.page,
        page_size: this.pagesize,
        start_at:
          this.formInline.consultationTime &&
          this.formInline.consultationTime.length > 0
            ? this.formatTime(this.formInline.consultationTime[0])
            : '',
        end_at:
          this.formInline.consultationTime &&
          this.formInline.consultationTime.length > 0
            ? this.formatTime(this.formInline.consultationTime[1])
            : '',
        message_type:
          this.activeName === 'all'
            ? ''
            : this.activeName === 'link'
            ? 'link'
            : 'image'
      }
      await SearchApi(params).then(res => {
        const data = res.data
        this.pageTotal = data.count
        if (!this.msgList || this.msgList.length === 0) {
          this.msgList = data.list
          return
        } else {
          if (data.list && data.list.length > 0) {
            data.list.forEach((item, index) => {
              this.msgList.push(item)
            })
          } else if (this.page === 1 && data.list.length === 0) {
            this.msgList = []
          }
          return
        }
      })
    },
    getPage() {
      this.page++
    },
    keywordChange() {
      this.page = 1
      this.msgList = []
      this.pageTotal = 0
      this.getMsgList()
    },
    getMsgListMore() {
      this.getMsgList()
    }
  }
}
</script>
<style lang="scss" scoped>
.MsgHistory {
  color: #000;
  .el-dialog {
    .el-dialog__header {
      .dialog-title {
        .title {
          margin-left: 50px;
          font-size: 14px;
          font-family: PingFangSC-Medium;
          color: #17233d;
        }
        .dialog__headerbtn_shrink {
          position: absolute;
          top: 7px;
          right: 60px;
          padding: 0;
          font-size: 14px;
          color: #909399;
          background: 0 0;
          border: none;
          outline: 0;
          cursor: pointer;
        }
        .dialog__headerbtn_full {
          position: absolute;
          top: 7px;
          right: 36px;
          padding: 0;
          font-size: 14px;
          color: #909399;
          background: 0 0;
          border: none;
          outline: 0;
          cursor: pointer;
        }
      }
    }

    .el-dialog__body {
      .search-box {
        padding: 0 12px;
      }
      .bodyheight {
        height: 520px;
        // overflow:auto;
      }
    }
  }
}
</style>
