<template>
  <div class="add-bout-dialog">
    <el-dialog
      title="申请追加回合"
      :visible="addBoutVisible"
      width="30%"
      center
      @close="addBoutCancel"
    >
      <el-row>
        <el-col :span="2">
          <i class="el-icon-warning fontsize-20"></i>
        </el-col>
        <el-col :span="22">
          <p
            class="content-text"
            v-for="(item, index) in contentText"
            :key="'contentText' + index"
          >
            {{ item }}
          </p>
        </el-col>
      </el-row>
      <div class="reason-box">
        <p class="title">追加原因</p>
        <el-input
          type="textarea"
          maxlength="100"
          show-word-limit
          placeholder="请输入内容"
          v-model="reason"
        >
        </el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="addBoutCancel">
          取消
        </el-button>
        <el-button size="small" type="primary" @click="addBoutConfirm">
          确认申请
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  props: {
    addBoutVisible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      reason: '',
      contentText: [
        '1、该功能仅针对复杂问题，实在无法在回合内完成的情况下使用！申请成功后，可不限回合次数，只能由服务人员结束，无需用户确认。',
        '2、请慎重使用该功能，避免用户后续依赖该功能，造成服务成本增加的问题！',
        '3、套餐回合结束后将无法申请！'
      ]
    }
  },
  methods: {
    addBoutConfirm() {
      if (!this.reason) {
        this.$message.warning('追加原因不能为空')
        return
      }
      this.$emit('addBoutConfirm', this.reason)
    },
    addBoutCancel() {
      this.$emit('addBoutCancel')
    }
  }
}
</script>
<style lang="scss" scope>
.add-bout-dialog {
  .content-text {
    line-height: 18px;
  }
  .reason-box {
    margin-top: 15px;
    .title {
      margin: 5px 0 10px;
    }
  }
}
</style>
