/* eslint-disable prettier/prettier */
<template>
  <div @click="replyBoxFlag">
    <app-header />
    <div class="chat_wrap">
      <div>
        <el-col :span="3">
          <div class="left-container">
            <drawer
              :title="
                listType === 'waiting'
                  ? '等待接入的用户'
                  : role === 'hm'
                  ? '咨询中的用户'
                  : '转诊中的用户'
              "
              :display.sync="quickList"
              :inner="true"
              :width="drawerWidth"
              :mask="false"
              @closeDrawer="closeDrawerFun"
            >
              <customer-in-drawer :list-type="listType" />
            </drawer>
            <el-tabs v-model="activeList" :stretch="true">
              <el-tab-pane label="消息" name="messageQueue">
                <message-queue />
              </el-tab-pane>
              <el-tab-pane label="用户" name="customer">
                <customer-list />
              </el-tab-pane>
            </el-tabs>
            <div class="footer">
              <div
                :class="listType === 'waiting' ? 'waiting_tab' : ''"
                @click="
                  listType = 'waiting'
                  quickList = true
                "
              >
                等待接入用户
                <span class="active">
                  {{ waitingCustomerList.length }}
                </span>
              </div>
              <div
                :class="listType === 'ongoing' ? 'ongoing_tab' : ''"
                @click="
                  listType = 'ongoing'
                  quickList = true
                "
              >
                <span>{{ role === 'hm' ? '咨询中' : '转诊中' }}</span>
                <span v-if="role === 'hm'" class="active">
                  {{ askingCustomerList.length }}
                </span>
                <span v-if="role !== 'hm'" class="active">
                  {{ transferCustomerList.length }}
                </span>
              </div>
            </div>
          </div>
        </el-col>
        <el-col v-show="msgQueue && msgQueue.length > 0" :span="3">
          <userInfo />
        </el-col>
        <el-col v-show="msgQueue && msgQueue.length > 0" :span="10">
          <div class="chat-container">
            <el-row class="chat-tab">
              <el-col :span="18">
                <!--IM模式：显示转诊状态；ROUND模式显示当前咨询状态是第几回合-->
                <transferStatus v-if="askMode === 'IM'"></transferStatus>
                <askingStatus v-if="askMode !== 'IM'"></askingStatus>
              </el-col>
              <el-col :span="6" class="return">
                <span @click="gotoDashboard">返回工作台</span>
              </el-col>
            </el-row>
            <div class="chat-main">
              <div class="chat-main-left">
                <div
                  class="chat-region"
                  :class="
                    askMode === 'IM' ? 'height-percent-70' : 'height-percent-55'
                  "
                >
                  <div
                    id="chatBox"
                    ref="chatListWrapper"
                    class="chat-list-wrapper"
                  >
                    <chatArea />
                    <GoBottom v-if="scrollBottomBtn" @goBottom="scrollBottom" />
                  </div>
                  <div v-if="askMode === 'ROUND'">
                    <transferStatus
                      class="round_transfer_status_box"
                    ></transferStatus>
                  </div>
                </div>
                <div
                  class="chat-input-wrapper"
                  :class="
                    askMode === 'IM' ? 'height-percent-26' : 'height-percent-37'
                  "
                >
                  <ChatInput />
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col v-if="msgQueue && msgQueue.length > 0" :span="3">
          <case-timeline />
        </el-col>
        <el-col v-if="msgQueue && msgQueue.length > 0" :span="5">
          <Extend :baby-id="activedBabyId" />
        </el-col>
        <el-col
          v-if="!msgQueue || msgQueue.length === 0"
          :span="21"
          class="default-box"
          type="flex"
          justify="center"
        >
          <el-row type="flex" justify="center">
            <div>
              <img src="@/assets/images/default.png" alt="" />
              <p>为母婴人群提供专业的医疗健康服务</p>
            </div>
          </el-row>
        </el-col>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import userInfo from './chat/userInfo'
import customerList from './chatlist/customerList'
import messageQueue from './chatlist/messageQueue'
import CustomerInDrawer from './chatlist/components/CustomerInDrawer'
import Drawer from './components/common/Drawer'
import Extend from './extend'

import CaseTimeline from './components/CaseTimeline'

import chatArea from './chat/chatArea'
import ChatInput from './chat/chatInput'
import transferStatus from './components/transferStatus'
import GoBottom from './chat/chatArea/components/GoBottom'
import { MessageRead } from '@/api/message'
import { AppHeader } from '@/layout/components/index'
import mywebim from '@/utils/im/mywebim.js' // 本地消息类型解析
import askingStatus from './components/askingStatus'
// import _ from 'lodash'
export default {
  components: {
    'app-header': AppHeader,
    askingStatus,
    transferStatus,
    messageQueue,
    customerList,
    userInfo,
    Drawer,
    Extend,
    CaseTimeline,
    CustomerInDrawer,
    chatArea,
    ChatInput,
    GoBottom
  },
  data() {
    return {
      MSG_ELEMENT_TYPE: mywebim.MSG_ELEMENT_TYPE,
      readTimer: false,
      haveReadFlag: false,
      box: '',
      msgAudio: '',
      quickList: false,
      drawerWidth: '100%',
      activeList: 'messageQueue',
      listType: '',
      chatMessage: '',
      beforeScrollTop: '',
      direction: '',
      oldtrigger: ''
    }
  },
  computed: {
    ...mapGetters([
      'askMode',
      'role',
      'scrollBottomBtn',
      'noMessageFlag',
      'msgQueue',
      'trigger',
      'current_fdAsk',
      'currentSession',
      // 'loginInfo',
      'activedBabyId',
      'lastAsk',
      'waitingCustomerList',
      'askingCustomerList',
      'getCurrentIMInfoMessages',
      'transferCustomerList'
    ])
  },
  created() {
    const params = {
      type: 'init',
      babyId: this.$route.query.babyId || '',
      userId: this.$route.query.userId || '',
      messageId: this.$route.query.messageId || '',
      sessionId: this.$route.query.sessionId || ''
    }
    this.$store.dispatch('chat/getCustomerTree', params, { root: true })
  },
  watch: {
    trigger(val, oldval) {
      this.oldtrigger = oldval
    },
    activeList(val) {
      if (val) {
        this.$store.commit('chat/SET_CHAT_LIST_TYPE', val, { root: true })
        this.$store.dispatch('chat/getCustomerTree', '', { root: true })
      }
    },
    currentSession(val, oldVal) {
      // 获取当前宝宝的聊天记录
      let sessionCardChange = false
      if (!oldVal) {
        sessionCardChange = true
      } else if (oldVal && val.session_id !== oldVal.session_id) {
        sessionCardChange = true
      } else if (oldVal && val.user._id !== oldVal.user._id) {
        sessionCardChange = true
      }
      if (sessionCardChange) {
        this.$store.dispatch('setCurrentIMInfoMessages', [], {
          root: true
        })
        this.$store.dispatch('chat/getLastC2CHistoryMessages', '', {
          root: true
        })
      }
    },
    getCurrentIMInfoMessages(newVal, oldVal) {
      this.$nextTick(() => {
        if (
          (this.trigger !== 'scroll' && this.oldtrigger !== 'scroll') ||
          this.trigger === 'changebaby'
        ) {
          this.$store.dispatch('setBottomBtn', false, { root: true }) // 是否显示直达最新消息按钮
          this.box.scrollTop = this.box.scrollHeight // 页面初始化，滚动条位于最下方
          this.beforeScrollTop = this.box.scrollTop
          this.sendRead()
          this.$store.dispatch('setMessageFlag', true, { root: true })
        }
        if (newVal && newVal !== null && newVal.length > 3) {
          var oldScrollTop = newVal[9]
            ? document.getElementById(newVal[9]._id).offsetTop
            : newVal[3]
            ? document.getElementById(newVal[3]._id).offsetTop
            : document.getElementById(newVal[0]._id).offsetTop
        }
        if (
          this.trigger === 'scroll' &&
          this.noMessageFlag &&
          this.direction === 'up'
        ) {
          this.box.scrollTop = oldScrollTop || this.box.scrollTop + 100
        } else if (
          this.trigger === 'scroll' &&
          !this.noMessageFlag &&
          this.direction === 'up'
        ) {
          this.box.scrollTop = oldScrollTop || 0
        }
      })
    }
  },
  mounted() {
    // 页面初始化，lask_ask设置为currentSession中的last_ask,不用请求接口
    setTimeout(() => {
      this.$store.commit('chat/SET_LAST_ASK', this.currentSession.last_ask)
      this.$store.commit(
        'chat/SET_SERVICE_ITEMS',
        this.currentSession.service_items
      )
    }, 2000)
    this.$nextTick(() => {
      this.box = this.$refs.chatListWrapper
      var $this = this
      // 监听这个dom的scroll事件
      this.box.onscroll = () => {
        const afterScrollTop = $this.box.scrollTop
        const scrollHeight = $this.box.scrollHeight
        const height = $this.box.clientHeight
        const cha = scrollHeight - afterScrollTop
        var delta = afterScrollTop - this.beforeScrollTop
        if (delta === 0) return false
        this.direction = delta > 0 ? 'down' : 'up'
        this.$store.dispatch('setTrigger', 'scroll', { root: true })
        if (this.direction === 'up' && afterScrollTop === 0) {
          if (this.noMessageFlag) {
            this.isScrollTop()
          }
        } else if (
          this.direction === 'down' &&
          this.noMessageFlag &&
          cha === height
        ) {
          this.isScrollDown()
        }
        if (this.readTimer) {
          clearTimeout(this.readTimer)
        }
        this.readTimer = setTimeout(() => {
          this.sendRead()
        }, 150)
        this.handleScroll()
      }
    })
  },
  methods: {
    closeDrawerFun() {
      this.quickList = false
      this.listType = ''
    },
    replyBoxFlag() {
      this.$store.dispatch('setReplyBoxFlag', false)
    },
    // goMessageDetail(message_id) {
    //   let existMesssage = {}
    //   const currentMegList = this.getCurrentIMInfoMessages
    //   if (currentMegList) {
    //     existMesssage = _.find(currentMegList, function(o) {
    //       return o._id === message_id
    //     })
    //   }
    //   if (existMesssage) {
    //     // 定位到当前锚点消息
    //     this.goAnchor(message_id)
    //   } else {
    //     this.$store.dispatch('chat/messageLocation', message_id, {
    //       root: true
    //     })
    //     setTimeout(() => {
    //       this.goAnchor(message_id)
    //     }, 700)
    //   }
    // },
    // goAnchor(selector) {
    //   var parentbox = document.getElementById('chatBox')
    //   var anchor = document.getElementById(selector) // 参数为要跳转到的元素id
    //   if (anchor && anchor.offsetTop) {
    //     parentbox.scrollTop = anchor.offsetTop - 150 // chrome
    //   }
    // },
    gotoDashboard() {
      this.$router.push({ path: '/dashboard' })
    },
    // ...mapActions({
    //   login: 'im/loginAsync'
    // }),
    monitorFdAskChangeFun(chatLi) {
      if (chatLi.getAttribute('fdAsk') !== this.current_fdAsk) {
        this.$store.dispatch('monitorFdAskChange', chatLi.getAttribute('fdAsk'))
      }
    },
    handleScroll() {
      var container = this.box
      if (!container) {
        return
      }
      const sumVal = container.scrollTop + container.clientHeight + 10
      const scrollHeights = container.scrollHeight
      const aa = sumVal - scrollHeights
      if (container.scrollTop > 0) {
        this.$store.dispatch('setBottomBtn', true, { root: true }) // 显示直达最新消息按钮
      }
      if (aa >= 0) {
        //  到底部了
        this.$store.dispatch('setBottomBtn', false, { root: true }) // 隐藏直达最新消息按钮
        this.$store.dispatch('monitorFdAskChange', this.lastAsk._id) // 点击直达最新消息，更新fd_ask
      }
    },
    sendRead() {
      const chatLi = document
        .getElementById('chat_list_wrapper')
        .getElementsByTagName('li')
      var container = this.$refs.chatListWrapper
      var swHeight = container.clientHeight
      const scrollTop = container.scrollTop
      const aa = swHeight + scrollTop
      for (let j = 0; j < chatLi.length; j++) {
        if (chatLi[j].offsetTop < aa - 100) {
          const isSelfSend = chatLi[j].getAttribute('isSelfSend')
          const msgStatus = chatLi[j].getAttribute('msgStatus')
          const msgType = chatLi[j].getAttribute('msgType')
          // 监听fd_ask的变化
          if (
            !isSelfSend &&
            !msgStatus &&
            msgType !== this.MSG_ELEMENT_TYPE.NOTICE
          ) {
            const _id = chatLi[j].getAttribute('id')
            this.sendReadApi(_id)
            this.haveReadFlag = true
          }
        }
        if (scrollTop < chatLi[j].offsetTop && chatLi[j].offsetTop < aa) {
          this.monitorFdAskChangeFun(chatLi[j])
        }
      }
    },
    sendReadApi(id) {
      if (id) {
        MessageRead(id).then(res => {
          if (res) {
            this.$store.dispatch('changeMessageReadSatus', id)
          }
        })
      }
    },
    // 监听是否滚动到最顶部，滚动到最顶部的时候继续加载上条消息
    isScrollTop() {
      const params = {
        message_id:
          this.getCurrentIMInfoMessages &&
          this.getCurrentIMInfoMessages.length > 0
            ? this.getCurrentIMInfoMessages[0]._id
            : '',
        direction: this.direction
      }
      this.$store.dispatch('chat/getLastC2CHistoryMessages', params, {
        root: true
      })
    },
    isScrollDown() {
      let msgId = ''
      if (
        this.getCurrentIMInfoMessages &&
        this.getCurrentIMInfoMessages.length > 0
      ) {
        // eslint-disable-next-line standard/computed-property-even-spacing
        msgId = this.getCurrentIMInfoMessages[
          this.getCurrentIMInfoMessages.length - 1
        ]._id
      }
      const params = {
        message_id: msgId,
        direction: this.direction
      }
      this.$store.dispatch('chat/getLastC2CHistoryMessages', params, {
        root: true
      })
    },
    scrollBottom() {
      this.$store.dispatch('setTrigger', '', { root: true })
      this.$store.dispatch('setCurrentIMInfoMessages', [], { root: true })
      this.$store.dispatch('setMessageFlag', true, { root: true })
      setTimeout(() => {
        this.$store.dispatch('chat/getLastC2CHistoryMessages', '', {
          root: true
        })
        this.$store.dispatch('setBottomBtn', false, { root: true }) // 显示直达最新消息按钮
      }, 300)
    }
  }
}
</script>
<style scoped lang="scss">
.height-percent-70 {
  height: 70.5%;
}
.height-percent-55 {
  height: 61%;
}
.height-percent-26 {
  height: 26%;
}
.height-percent-37 {
  height: 32%;
}
.message-card {
  display: flex;
}

.default-box {
  height: 100%;
  min-height: 600px;
  background: #f6f7fb;
  p {
    margin-top: 30px;
    font-size: 18px;
    font-family: PingFangSC-Regular;
    color: #8e90a3;
    letter-spacing: 0;
    margin-bottom: 300px;
  }
  img {
    margin: 250px auto 0;
    text-align: center;
    vertical-align: middle;
  }
}
.chat_wrap {
  overflow: hidden;
  height: 100%;
  max-height: 870px;
}
.return {
  float: right;
  padding-right: 8px;
  line-height: 38px;
  font-size: 14px;
  text-align: right;
  color: #0085ff;
  span {
    cursor: pointer;
  }
}
#audioTip {
  position: absolute;
  top: 15px;
  right: 250px;
  width: 100px;
  height: 20px;
  border: 1px solid red;
}
.left-container {
  position: relative;
  .footer {
    position: relative;
    bottom: 5px;
    display: flex;
    width: 100%;
    height: 40px;
    background-color: #fff;
    border: 1px solid #eee;
    border-radius: 5px;
    cursor: pointer;
    .waiting_tab,
    .ongoing_tab {
      border: 1px solid #409eff;
    }
    div {
      line-height: 40px;
      text-align: center;
      flex: 1;
      &:first-child {
        border-right: 1px solid #eee;
      }
      &.waiting_tab:first-child {
        border-right: 1px solid #409eff;
      }
    }
    .active {
      color: #409eff;
    }
  }
}
.chat-container {
  .chat-main {
    display: flex;
    height: 868px;
    border: 1px solid #eee;
    border-top: 2px solid #eee;
    &-left {
      flex: 3;
      .chat-region {
        position: relative;
        display: flex;
        width: 100%;
        flex-grow: 1;
        flex-direction: column;
        .round_transfer_status_box {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          z-index: 20;
        }
        .chat-tab {
          .chat-tip {
            padding: 3px 20px;
            text-indent: 28px;
          }
        }
        .chat-list-wrapper {
          overflow-x: hidden;
          min-width: 300px;
          max-width: 860px;
          background: #f6f7fb;
          -moz-transition: width 2s;
          -webkit-transition: width 2s;
          -o-transition: width 2s; /* Opera */
          transition: width 2s;
          flex-grow: 1;
        }
        .chat-input-wrapper {
          position: absolute;
          bottom: 0;
          width: 100%;
          height: 22%;
          min-height: 150px;
          border-top: 1px solid #eee;
        }
      }
    }
    &-right {
      flex: 1;
    }
  }
  .chat-input-wrapper {
    background: #f6f7fb;
    box-shadow: inset 0 1px 0 0 #e8eaec;
  }
}
</style>
