<template>
  <div>
    <div class="card-title">
      <div>{{ getTimeLineDom(timePot).name }}</div>
      <div>
        {{ getTimeLineDom(timePot).title }}
      </div>
    </div>
    <div class="customer-info">
      {{ getTimeLineDom(timePot).value }}
    </div>
    <div class="card-time">
      {{ new Date(timePot.created_at) | formatTime() }}
    </div>
  </div>
</template>
<script>
export default {
  props: {
    timePot: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      sas: ''
    }
  },
  methods: {
    getTimeLineDom(timePot) {
      let obj = {}
      const type = timePot.type
      switch (type) {
        case 'transfer_apply':
          obj = {
            name: timePot.user.name,
            title: '发起转诊请求',
            value: '原因：' + timePot.reason
          }
          break
        case 'transfer_reject':
          obj = {
            name: timePot.user.name,
            title: '拒绝转诊请求',
            value: '原因：' + timePot.transfer_reject_reason
          }
          break
        case 'transfer_start':
        case 'transfer_accept':
        case 'voice_transfer_end':
        case 'admin_end':
        case 'round_append':
        case 'system':
        case 'user_end':
          obj = {
            name:
              timePot.type === 'user_end'
                ? '用户'
                : timePot.type === 'system'
                ? '系统'
                : timePot.user.name,
            title: timePot.title,
            value: ''
          }
          break
        default:
          return obj
      }
      return obj
    }
  }
}
</script>
