<template>
  <div class="transfer">
    <span>
      <el-tooltip
        :content="!hasServer ? '该用户套餐不包含此服务' : tipContent"
        placement="top"
      >
        <svg-icon
          v-if="iconType === 'apply'"
          class="transferApplyBtn"
          :class="!hasServer || !transferBtn ? 'notAllow' : ''"
          icon-class="transfer-apply"
          @click="applyTransfer"
        />
        <svg-icon
          v-else-if="iconType === 'accept'"
          class="transferAcceptBtn"
          icon-class="transfer"
          @click="startTransfer"
        />
        <svg-icon
          v-else-if="iconType === 'success'"
          class="transferSuccessBtn"
          icon-class="transfer"
          @click="startTransferNot"
        />
      </el-tooltip>
    </span>
    <el-dialog
      class="transfer_dialog"
      :title="noSuggest ? '请填写用户情况' : '转诊请求'"
      :visible.sync="diagnoseVisible"
      :width="noSuggest ? '20%' : '30%'"
      center
      :before-close="handleClose"
    >
      <div v-if="!noSuggest">
        <el-form
          ref="ruleForm"
          :model="ruleForm"
          :rules="rules"
          label-width="100px"
          label-position="top"
          class="demo-ruleForm"
        >
          <el-form-item label="请选择向谁发起转诊请求" prop="user">
            <el-radio-group v-model="ruleForm.user" style="width:100%;">
              <el-radio-button
                v-for="(item, index) in radioChoice"
                :key="item._id + index"
                :label="item"
                :disabled="
                  !itemDisabled(item.bus_role) ||
                    (item.online_status === 'online' ? false : true)
                "
                border
              >
                <div class="user_icon_name_status">
                  <img
                    class="status_icon"
                    :src="getStatusImg(item.online_status)"
                  />
                  <span class="user_name_status">
                    &nbsp;&nbsp;{{ item.name }} &nbsp;&nbsp;</span
                  >
                  <el-tooltip
                    :disabled="itemDisabled(item.bus_role)"
                    :content="
                      !itemDisabled(item.bus_role)
                        ? '该用户套餐不包含此服务'
                        : ''
                    "
                    placement="top"
                  >
                    <span class="user_status">{{
                      userStatus(item.online_status)
                    }}</span>
                  </el-tooltip>
                </div>
                <p
                  v-if="item.online_status === 'hang_up'"
                  class="hang_up_reason align-left"
                >
                  {{ item.hang_up_reason }}
                </p>
                <p
                  v-if="item.online_status === 'hang_up'"
                  class="predict_online_at align-left"
                >
                  预计上线时间：{{
                    item.predict_online_at
                      ? formatTime(item.predict_online_at)
                      : ''
                  }}
                </p>
              </el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="转诊原因" prop="reason">
            <el-input
              v-model="ruleForm.reason"
              placeholder="请输入转诊原因"
              type="textarea"
              maxlength="100"
              :rows="3"
              show-word-limit
            />
          </el-form-item>
        </el-form>
      </div>
      <div v-if="noSuggest" class="suggest-info">
        请在当前转诊轨迹区域填写主诉和建议（或叮嘱）后再发起转诊申请，方便转诊成员快速了解用户情况，做出是否同意转诊的判断
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          size="small"
          :disabled="transfer_button_status || loading"
          @click="confirmTransfer"
          >{{ noSuggest ? '返回填写' : '确认转诊' }}</el-button
        >
      </span>
    </el-dialog>
    <Dialog
      v-model="userIntentDialog"
      :title="'请回复用户转诊意向'"
      :btnText="'确 定'"
      :width="'450px'"
      @btnHandle="closeUserIntentDialog"
    >
      <div slot="content">
        <p class="margin-bottom-10">
          请先回复用户转诊意向后，再发起转诊申请，及时响应用户。
        </p>
      </div>
    </Dialog>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { transferApplyApi, getChoice, startTransfer } from '@/api/diagnose'
import { userStatus, getStatusImg } from '@/filters'
import Dialog from '@/components/Dialog'
import _ from 'lodash'
export default {
  // 发送自定义消息
  name: 'Transfer',
  props: {
    hasServer: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Dialog
  },
  data() {
    return {
      userIntentDialog: false,
      diagnoseVisible: false,
      noSuggest: true, // store中存储的医生建议是否为空,true表示为空
      suggestStatus: '',
      suggestContent: '',
      iconType: 'apply',
      transfer_button_status: true, // 确认转诊按钮是否可用
      loading: false,
      ruleForm: {
        user: '',
        reason: ''
      },
      radioChoice: [],
      tipContent: '申请转诊',
      rules: {
        user: [
          { required: true, message: '请选择向谁发起转诊请求', trigger: 'blur' }
        ],
        reason: [{ required: true, message: '请输入转诊原因', trigger: 'blur' }]
      },
      transferBtn: false
    }
  },
  computed: {
    ...mapGetters([
      'userInfo',
      'currentTextAnswer',
      'lastAsk',
      'askMode',
      'sessionId',
      'serviceItem',
      'activedBaby',
      'activedBabyId'
    ])
  },
  watch: {
    // 咨询轨迹的textAnswer
    currentTextAnswer(val) {
      this.suggestStatus =
        val && val.status === 'resolved' ? '已解决' : '未解决'
      this.suggestContent = val && val.suggestion ? val.suggestion : ''
      if (!this.suggestStatus || !this.suggestContent) {
        this.noSuggest = true // 测试弹窗
      } else {
        this.noSuggest = false
      }
    },
    lastAsk: {
      handler: function(val) {
        if (val) {
          this.transferStatus()
        }
      },
      deep: true
    },
    activedBaby(val) {
      if (val) {
        this.getTransferBtnStatus()
      }
    },
    radioChoice(val) {
      const oo = _.filter(val, function(o) {
        return o.online_status === 'online'
      })
      if (oo && oo.length > 0) {
        this.transfer_button_status = false
      } else {
        this.transfer_button_status = true
      }
    }
  },
  methods: {
    userStatus,
    getStatusImg,
    closeUserIntentDialog() {
      this.userIntentDialog = false
    },
    showUserIntentDialog() {
      this.userIntentDialog = true
    },
    getUserIntent() {
      if (this.askMode === 'ROUND') {
        //用户有转诊意向且已确认用户的转诊意向
        if (
          this.lastAsk.round.is_apply_transfer &&
          this.lastAsk.round.apply_transfer_handled
        ) {
          this.transferBtn = true
        } else if (!this.lastAsk.round.is_apply_transfer) {
          // 需求： 用户没有表达转诊意向的时候，健康管理师仍可以发起转诊申请，暂时去掉下面判断
          // this.transferBtn = false
          // this.$message.info('用户没有转诊意向')
        } else if (
          this.lastAsk.round.is_apply_transfer &&
          !this.lastAsk.round.apply_transfer_handled
        ) {
          // 没有确认用户的转诊意向,弹窗提示
          this.transferBtn = false
          this.showUserIntentDialog()
        }
      }
    },
    getTransferBtnStatus() {
      if (this.activedBaby.is_asking) {
        this.transferBtn = true
      } else {
        this.transferBtn = false
      }
    },
    startTransferNot() {
      this.$message({
        type: 'warning',
        message: '正在转诊中，不能发起转诊'
      })
    },
    transferStatus() {
      if (this.lastAsk.status !== 'ask_end' && this.lastAsk.has_transfer) {
        if (this.lastAsk.transfer_status === 'accept') {
          this.iconType = 'accept'
          this.tipContent = '发起转诊'
        } else if (
          this.lastAsk.transfer_status === 'wait_user' ||
          this.lastAsk.transfer_status === 'asking' ||
          this.lastAsk.transfer_status === 'start'
        ) {
          this.iconType = 'success'
          this.tipContent = '发起转诊'
        } else {
          this.iconType = 'apply'
          this.tipContent = '申请转诊'
        }
      } else {
        this.iconType = 'apply'
        this.tipContent = '申请转诊'
      }
    },
    itemDisabled(name) {
      let index = false
      for (let i = 0; i < this.serviceItem.length; i++) {
        if (this.serviceItem[i].indexOf(name) !== -1) {
          index = true
          return index
        } else if (
          // TODOS:
          this.serviceItem[i] === 'common_text_transfer' ||
          this.serviceItem[i] === 'common_voice_transfer'
        ) {
          index = true
          return index
        }
      }
      return index
    },
    getChoiceFun() {
      getChoice(this.sessionId).then(res => {
        if (res && res.code === 20000) {
          this.radioChoice = res.data
        }
      })
    },
    applyTransfer() {
      if (!this.hasServer) {
        return
      }
      if (!this.transferBtn) {
        return
      }
      if (this.lastAsk.transfer_status === 'apply') {
        this.$message({
          type: 'warning',
          message: '正在申请中'
        })
        return
      }
      if (!this.noSuggest) {
        this.getChoiceFun()
      } else {
        this.transfer_button_status = false
      }
      this.diagnoseVisible = true
      this.ruleForm.reason = ''
    },
    confirmTransfer() {
      // 确认接受转诊
      if (this.noSuggest) {
        this.handleClose()
        return
      } else {
        // 确认转诊接口
        this.confirmApi()
      }
    },
    confirmApi() {
      const session_id = this.lastAsk._id
      const params = {
        user_id: this.ruleForm.user._id,
        bus_role: this.ruleForm.user.bus_role,
        transfer_reason: this.ruleForm.reason
      }
      this.loading = true
      this.$refs['ruleForm'].validate(valid => {
        if (valid) {
          if (session_id) {
            transferApplyApi(session_id, params)
              .then(res => {
                const operatorName = '申请转诊'
                this.loading = false
                this.$message({
                  type: res.data.result ? 'success' : 'error',
                  message: res.data.result
                    ? operatorName + '成功'
                    : operatorName + '失败'
                })
                this.handleClose()
              })
              .catch(error => {
                console.log(error)
                this.loading = false
              })
          }
        } else {
          this.loading = false
          return
        }
      })
    },
    startTransfer() {
      if (!this.lastAsk._id) {
        return
      }
      this.getUserIntent()
      if (!this.transferBtn) {
        return
      }
      startTransfer(this.lastAsk._id).then(res => {
        const operatorName = '发起转诊'
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result
            ? operatorName + '成功'
            : operatorName + '失败'
        })
      })
    },
    handleClose() {
      this.diagnoseVisible = false
    }
  }
}
</script>
<style lang="scss" scoped>
.transfer {
  .transferApplyBtn {
    color: #000;
  }
  .transferSuccessBtn {
    margin-top: 7px;
    font-size: 16px;
    color: grey;
  }
  .transferAcceptBtn {
    margin-top: 7px;
    font-size: 16px;
    color: #0085ff;
  }
  .user_icon_name_status {
    line-height: 18px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    text-align: left;
    color: #576277;
    letter-spacing: 0;
  }
  .hang_up_reason,
  .predict_online_at {
    margin-left: 25px;
    line-height: 17px;
    font-size: 12px;
    font-family: PingFangSC-Regular;
    color: #999;
    letter-spacing: 0;
  }
  label.el-radio-button {
    width: 100%;
  }
  .el-radio-button__inner {
    border-left: 1px solid #dcdfe6;
  }
}
form.el-form.demo-ruleForm.el-form--label-top {
  overflow-y: auto;
  max-height: 440px;
}
</style>
