<template>
  <div class="send-video">
    <el-tooltip placement="top">
      <div slot="content">
        <p v-if="!hasServer">该用户套餐不包含此服务</p>
        <div v-else>
          <p v-if="uploading" :loading="uploading">
            <i class="el-icon-loading" /> 上传中
          </p>
          <p v-else>发送视频</p>
        </div>
      </div>
      <el-upload
        :disabled="!hasServer || submitBtnStatusDisabled || uploading"
        :loading="uploading"
        class="avatar-uploader"
        action
        :show-file-list="false"
        :on-error="handleError"
        :on-success="handleAvatarSuccess"
        accept="video/*"
        :http-request="uploadFunc"
        :before-upload="beforeAvatarUpload"
      >
        <svg-icon
          icon-class="scshipin"
          :class="!hasServer ? 'notAllow' : ''"
          class="video_icon"
        />
      </el-upload>
    </el-tooltip>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { handleMessageSend } from '@/api/message'
import { upload } from '@/api/upload'
export default {
  name: 'SendVideo',
  props: {
    submitBtnStatusDisabled: {
      type: Boolean,
      default: true
    },
    hasServer: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      uploading: false
    }
  },
  computed: {
    ...mapGetters(['lastAsk'])
  },
  methods: {
    beforeAvatarUpload(file) {
      const isSuccessType =
        [
          'video/mp4',
          'video/avi',
          'video/flv',
          'video/wmv',
          'video/mkv',
          'video/mov'
        ].indexOf(file.type) >= 0
      if (!isSuccessType) {
        this.$message.error('支持avi、flv、mp4、wmv、mkv、mov、等视频格式')
      }
      return isSuccessType
    },
    // 上传图
    async uploadFunc(option) {
      this.uploading = true
      if (!this.lastAsk._id) return
      const currentFdAsk = this.lastAsk._id
      window.sessionStorage.setItem('currentFdAsk', currentFdAsk)
      const data = await upload(option.file, progress => {})
      if (data.url) {
        this.sendVideoMsg(data)
      }
    },
    handleAvatarSuccess(res, file) {
      this.uploading = false
    },
    handleError(res, file) {
      this.uploading = false
    },
    // 发送视频消息
    sendVideoMsg(res) {
      const params = {
        fd_ask: sessionStorage.getItem('currentFdAsk'), // 咨询id,
        message_body: {
          // 消息体
          type: 'video',
          url: res.url,
          cover: ''
        }
      }
      handleMessageSend(params)
        .then(res => {
          if (res.data.result) {
            this.$store.dispatch('pushCurrentIMInfoMessages', res.data.message)
            this.$store.dispatch('setBottomBtn', false, { root: true }) // 隐藏直达最新消息按钮
          } else {
            this.$message.error(res.data.message)
          }
          window.sessionStorage.removeItem('currentFdAsk')
        })
        .catch(err => {
          window.sessionStorage.removeItem('currentFdAsk')
          console.error('发送视频：', err)
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.video_icon {
  color: #000;
}
</style>
