<template>
  <PageCommon>
    <div v-for="(group, index) in customerTree" :key="index" class="group-card">
      <div class="group-card-title">
        <svg-icon
          v-if="group.is_mine"
          class="icon-avatar"
          icon-class="myteam"
        />
        <div v-else class="bg-blue">{{ group.name[0] }}</div>
        <!-- 团队 -->
        {{ group.is_mine ? '我的团队' : group.name }}
        <el-tooltip class="item" effect="dark" content="代班中" placement="top">
          <svg-icon
            v-show="role === 'doctor' && group.is_replace ? true : false"
            class="group_replace_icon"
            icon-class="yxdaiban1"
          />
        </el-tooltip>
      </div>
      <div v-for="(nurse, subIndex) in group.hms" :key="`nurse${subIndex}`">
        <toggle-box :child-sessions="nurse.sessions">
          <template v-slot:toggleTitle>
            <div class="group-card-subtitle">
              <div class="nurse-name">
                <!-- 健康管理师 -->
                {{ nurse.name }}({{ nurse.server_num }}/{{
                  nurse.server_limit
                }})
                <el-tooltip
                  class="item"
                  effect="dark"
                  content="代班中"
                  placement="top"
                >
                  <svg-icon
                    v-show="
                      role !== 'doctor' && nurse.is_replace ? true : false
                    "
                    class="replace_icon"
                    icon-class="yxdaiban1"
                  />
                </el-tooltip>
              </div>
            </div>
          </template>
          <template v-slot:toggleContent>
            <div v-for="(customer, cusIndex) in nurse.sessions" :key="cusIndex">
              <!-- 用户 -->
              <session-card
                :session="customer"
                :edit="true"
                :update-remarkname="updateRemarkName"
              />
            </div>
          </template>
        </toggle-box>
      </div>
    </div>
  </PageCommon>
</template>

<script>
import PageCommon from './components/PageCommon'
import SessionCard from './components/SessionCard'
import ToggleBox from '../components/common/ToggleBox'
import { mapGetters } from 'vuex'
import { updateRemarkName } from '@/api/chat'
export default {
  name: 'CustomerList',
  components: {
    PageCommon,
    SessionCard,
    ToggleBox
  },
  data() {
    return {
      searchText: ''
    }
  },
  computed: {
    ...mapGetters(['customerTree', 'role']),
    cusSearchValue: {
      get() {
        return this.$store.state.chat.cusSearchValue
      },
      set(searchValue) {
        this.$store.commit('chat/SET_CUS_SEARCHVALUE', searchValue)
      }
    }
  },
  methods: {
    // 修改用户备注名
    updateRemarkName(customer, remarkName) {
      // 修改用户备注名接口 模拟接口
      updateRemarkName(customer.session_id, remarkName).then(res => {
        if (res.code === 20000) {
          this.$message.success(res.data.message)
          // 刷新聊天窗口顶部客户信息
          this.$store.dispatch(
            'chat/getActivedUserInfo',
            {},
            {
              root: true
            }
          )
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.bg-blue {
  margin-right: 7px;
  width: 29px;
  height: 29px;
  line-height: 30px;
  font-size: 10px;
  text-align: center;
  color: #fff;
  background: #0085ff;
  border-radius: 50%;
}
.group-card {
  user-select: none;
  border-bottom: 1px solid #eee;
  &-title {
    display: flex;
    align-items: center;
    padding: 15px;
    color: #0085ff;
    .group_replace_icon {
      margin-left: 5px;
      font-size: 20px;
    }
    .icon-avatar {
      margin-right: 10px;
      font-size: 30px;
    }
  }
  &-subtitle {
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    cursor: pointer;
    .nurse-name {
      font-size: 16px;
      color: #17233d;
      .replace_icon {
        margin-left: 5px;
        font-size: 18px;
      }
    }
  }
}
</style>
