<template>
  <div class="phone_input">
    <!-- 电话转诊 -->
    <p v-if="status" class="phone">
      请用{{ customPhone }}拨打<span>{{ userPhone }}</span>
    </p>
    <p class="tip margin-top-10">{{ tip }}</p>
    <p class="button_p">
      <el-button
        type="primary"
        :disabled="loading"
        size="small"
        @click="startTransfer"
        >{{ btnTxt }}</el-button
      >
      <span v-if="status" class="awite-time">
        <span class="tip">电话接诊中：</span>
        <span class="time-style">{{ hour }}</span
        ><span class="time-unit">:</span>
        <span class="time-style">{{ minute }}</span
        ><span class="time-unit">:</span>
        <span class="time-style">{{ second }}</span>
      </span>
    </p>
  </div>
</template>
<script>
import { doctoreStartTransfer, getPhoneNumber } from '@/api/diagnose'
import { mapGetters } from 'vuex'
export default {
  props: {
    showVoice: {
      type: Boolean,
      default: false
    },
    lastAskId: {
      type: String,
      default: ''
    },
    hour: {
      type: String,
      default: '00'
    },
    minute: {
      type: String,
      default: '00'
    },
    second: {
      type: String,
      default: '00'
    }
  },
  data() {
    return {
      loading: false,
      customPhone: '13000000000',
      userPhone: '12555444555',
      status: false,
      time: '',
      tip:
        '请您单击下方【开始电话转诊】按钮绑定号码后，根据提示拨打电话开始电话转诊',
      btnTxt: '开始电话转诊'
    }
  },
  computed: {
    ...mapGetters(['lastAsk'])
  },
  watch: {
    showVoice(val) {
      if (val) {
        this.isStartPhone()
      }
    }
  },
  mounted() {
    if (this.showVoice) {
      this.isStartPhone()
    }
  },
  methods: {
    isStartPhone() {
      getPhoneNumber(this.lastAskId).then(res => {
        if (!res.data.result) return
        this.customPhone = res.data.from
        this.userPhone = res.data.to
        this.status = true
        this.tip = '请您单击下方【结束电话转诊】的按钮，结束此次电话转诊'
        this.btnTxt = '结束电话转诊'
        this.$emit('doctorStartTransfer')
        this.$store.dispatch('chat/getCustomerTree')
      })
    },
    startTransfer() {
      this.loading = true
      if (this.status) {
        this.$emit('doctorCloseTransfer', '')
        this.loading = false
        return
      }
      const fd_ask_transfer = this.lastAsk._id
      doctoreStartTransfer(fd_ask_transfer)
        .then(res => {
          // 开始电话转诊
          this.loading = false
          if (res && res.data.result) {
            this.customPhone = res.data.from
            this.userPhone = res.data.to
            this.status = true
            this.tip = '请您单击下方【结束电话转诊】的按钮，结束此次电话转诊'
            this.btnTxt = '结束电话转诊'
            this.$emit('doctorStartTransfer')
            this.$store.dispatch('chat/getCustomerTree')
          }
        })
        .catch(error => {
          console.log(error)
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.phone_input {
  position: absolute;
  top: 0;
  left: 0;
  padding: 30px 40px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  p {
    line-height: 16px;
    font-size: 16px;
    font-family: PingFangSC-Medium;
    color: #fff;
    letter-spacing: 0;
    span {
      color: #f8747f;
    }
  }
  p.tip {
    font-size: 12px;
    font-family: PingFangSC-Regular;
  }
  .button_p {
    margin-top: 20px;
    .el-button--primary {
      color: #fff;
      background: #0085ff;
      border: none;
    }
    .awite-time {
      .tip {
        margin-left: 50px;
        font-size: 14px;
        color: #fff;
      }
      .time-style {
        margin-left: 4px;
        padding: 0 1px;
        line-height: 14px;
        font-size: 14px;
        font-family: PingFangSC-Regular;
        color: #fff;
        background: #17233d;
        border-radius: 2px;
        letter-spacing: 0;
      }
      .time-unit {
        margin-left: 4px;
        color: #17233d;
      }
    }
  }
}
</style>
