<template>
  <div>
    <div ref="block" :class="{ collapse: collapsed === 1 }">
      {{ title }}:
      <span class="collapse_text">{{ collapseText }}</span>
    </div>
    <div v-if="collapsed !== 0" class="collapse-tag" @click="toggle">
      {{ collapsedTag }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'CollapseBox',
  props: {
    collapseText: {
      type: String,
      default: ''
    },
    textLength: {
      type: Number,
      default: 42
    },
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      show: false,
      collapsed: 0
    }
  },
  computed: {
    collapsedTag() {
      switch (this.collapsed) {
        case 1:
          return '展开'
        case 2:
          return '收起'
        default:
          return ''
      }
    }
  },
  methods: {
    toggle() {
      if (this.collapsed === 1) {
        this.collapsed = 2
      } else if (this.collapsed === 2) {
        this.collapsed = 1
      }
    }
  },
  mounted() {
    if (this.$refs.block.offsetHeight > 60) {
      this.collapsed = 1
    }
  }
}
</script>

<style scoped lang="scss">
.collapse_text {
  word-break: break-all;
  white-space: pre-line;
}
.collapse-tag {
  padding-right: 15px;
  line-height: 15px;
  text-align: right;
  color: #2d8cf0;
  cursor: pointer;
}
.collapse {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.block {
  display: block;
}
</style>
