<template>
  <div>
    <!-- 转诊状态组件 -->
    <div>
      <span
        v-if="
          lastAsk &&
            lastAsk.family_member === activedBabyId &&
            (lastAsk.has_transfer || lastAsk.transfer_status === 'failed') &&
            lastAsk.status !== 'ask_end'
        "
        class="transfer_status_box"
        :class="
          diagnoseStatue && askMode !== 'IM'
            ? 'round-diagnose-status'
            : diagnoseStatue
            ? 'im-diagnose-status'
            : ''
        "
      >
        <span class="text">{{ diagnoseStatue }}</span>
        <span v-if="timeCha" class="awite-time">
          <span class="time-style"
            ><span v-if="timeCha.hours">{{ timeCha.hours }}</span></span
          ><span class="time-unit">:</span>
          <span class="time-style">{{ timeCha.minutes }}</span
          ><span class="time-unit">:</span>
          <span class="time-style">{{ timeCha.seconds }}</span>
        </span>
      </span>
    </div>
  </div>
</template>
<script>
import { waitTime } from '@/utils/moment'

import { mapGetters } from 'vuex'
import { countTime } from '@/utils'
import { TransferStatusText, TransferStatusTime } from '@/filters'
export default {
  data() {
    return {
      transferStatus: false,
      diagnoseStatue: '',
      time: {
        startTime: '',
        endTime: ''
      },
      transferTimer: '',
      timeCha: ''
    }
  },
  computed: {
    ...mapGetters(['askMode', 'lastAsk', 'activedBabyId', 'role'])
  },
  watch: {
    lastAsk: {
      handler: function(val) {
        if (val) {
          const value = this.lastAsk.transfer_status
          const user = this.lastAsk.transfer_user
          const type = this.lastAsk.transfer_type
          const isAuto = this.lastAsk.is_transfer_auto_accept
            ? this.lastAsk.is_transfer_auto_accept
            : false
          this.diagnoseStatue = this.TransferStatusText(
            value,
            user,
            type,
            isAuto,
            this.role
          )
          this.time = this.TransferStatusTime(
            value,
            this.lastAsk,
            isAuto,
            this.role
          )
          if (this.transferTimer) {
            clearInterval(this.transferTimer)
          }
          this.timeFormatter()
        }
      },
      deep: true
    }
  },
  mounted() {
    this.timeFormatter()
  },
  methods: {
    TransferStatusText,
    TransferStatusTime,
    countTime,
    waitTime,
    timeFormatter() {
      // 有开始时间和结束时间时，倒计时
      if (this.time && this.time.startTime && this.time.endTime) {
        this.timeCha = this.countTime(this.time.startTime, this.time.endTime)
        this.transferTimer = setInterval(() => {
          this.timeCha = this.countTime(this.time.startTime, this.time.endTime)
        }, 1000)
      } else if (this.time && this.time.startTime && !this.time.endTime) {
        // 有开始时间无结束时间时，计时
        this.timeCha = this.waitTime(this.time.startTime, 'obj')
        this.transferTimer = setInterval(() => {
          this.timeCha = this.waitTime(this.time.startTime, 'obj')
        }, 1000)
      } else {
        this.timeCha = ''
      }
      if (!this.timeCha || (!this.timeCha.seconds && !this.timeCha.minutes)) {
        clearInterval(this.transferTimer)
      }
    }
  }
}
</script>
<style lang="scss">
.round-diagnose-status {
  display: inline-block;
  padding-left: 10px;
  width: 100%;
  height: 30px;
  line-height: 30px;
  color: #fff;
  background: rgba(86, 94, 118, 0.64);
}
.im-diagnose-status {
  margin-left: 10px;
  padding: 4px 14px;
  height: 40px;
  line-height: 40px;
  color: #576277;
  background: #f6f7fb;
  .text {
    line-height: 14px;
    font-size: 12px;
    font-family: PingFangSC-Medium;
    color: #576277;
    letter-spacing: 0;
  }
}
.transfer_status_box {
  .awite-time {
    .time-style {
      margin-left: 4px;
      padding: 0 2px;
      line-height: 14px;
      font-size: 14px;
      font-family: PingFangSC-Regular;
      color: #fff;
      background: #17233d;
      border-radius: 2px;
      letter-spacing: 0;
    }
    .time-unit {
      margin-left: 4px;
    }
  }
}
</style>
