import request from '@/utils/request'

// 创建健康随访自定义任务
export function createFollowTask(data) {
  return request({
    url: '/task_manager/chanke/task',
    method: 'post',
    data: data
  })
}

// 直接发送健康随访任务 点击保存按钮或者预览按钮时都需要掉该创建接口

export function updateTaskContent(data) {
  return request({
    url: '/task_manager/chanke/task_message',
    method: 'post',
    data: data
    // mock: true
  })
}

// 从去发送进入，点击保存
export function updateSavedTaskContent(id, data) {
  return request({
    url: `/task_manager/chanke/task/${id}/save`,
    method: 'put',
    data: data
    // mock: true
  })
}
// 任务发送
export function sendTask(taskId) {
  return request({
    url: `task_manager/chanke/task/${taskId}/send`,
    method: 'put'
    // mock: true
  })
}

// 关闭任务
export function closeTask(taskId, data) {
  return request({
    url: `/task_manager/chanke/task/${taskId}/close`,
    method: 'put',
    data
  })
}

// 获取预览信息
export function previewTask(taskId) {
  return request({
    url: `/task_manager/chanke/task/${taskId}/preview`,
    method: 'get'
  })
}

// 获取任务详情
export function getTaskContent(taskId) {
  return request({
    url: `/task_manager/chanke/task/${taskId}/detail`,
    method: 'get'
    // mock: true
  })
}

// 获取任务列表
export function getAllTaskList(params) {
  return request({
    url: '/task_manager/chanke/tasks',
    method: 'get',
    params: params
    // mock: true
  })
}

// 回复留言
export function replayUser(taskId, data) {
  return request({
    url: `/task_manager/chanke/task/${taskId}/reply_msg`,
    method: 'put',
    data: data
    // mock: true
  })
}
