<template>
  <ul id="chat_list_wrapper" ref="UL">
    <li
      v-for="(item, index) in currentIMInfoMessages"
      :id="item._id"
      :key="index"
      :isSelfSend="item.isSelfSend"
      :msgStatus="item.msgStatus"
      :fdAsk="item.fdAsk"
      :class="item.fdAsk"
      class="clearfix"
    >
      <div
        v-if="!item.msgType && item.msgOptions.show !== false"
        :class="item.isSelfSend ? 'textRight' : 'textLeft'"
      >
        <!-- 左侧头像 -->
        <div v-if="!item.isSelfSend" class="headImage">
          <span
            class="user-avatar"
            :style="{ backgroundImage: `url(${item.fromAccountImage})` }"
          />
          <span class="time">
            <span class="name" style="margin-right:10px;">{{
              item.fromAccountNick
            }}</span>
            {{ formatTime(item.msgTime) }}
          </span>
        </div>
        <div class="msg-box">
          <div
            class="chat-wrapper"
            :class="
              item.msgOptions.type === MSG_ELEMENT_TYPE.IMAGE ||
              item.msgOptions.type === MSG_ELEMENT_TYPE.VIDEO ||
              item.msgOptions.type === MSG_ELEMENT_TYPE.VOICE
                ? 'image_wrapper'
                : ''
            "
            :style="
              item.isSelfSend ? 'margin-right: 50px;' : 'margin-left: 50px;'
            "
          >
            <chatMsg :msgOptions="item.msgOptions" :item="item"></chatMsg>
          </div>
          <span
            v-if="item.msgOptions.type !== MSG_ELEMENT_TYPE.VOICE"
            class="msg-status"
            :class="item.msgStatus ? 'read-msg-status' : 'unread-msg-status'"
            >{{ item.msgStatus ? '已读' : '未读' }}</span
          >
        </div>
        <!-- 右侧头像 -->
        <div v-if="item.isSelfSend" class="headImage">
          <span class="time">
            {{ formatTime(item.msgTime) }}
            <span class="name" style="margin-left:10px;">{{
              item.fromAccountNick
            }}</span>
          </span>
          <span
            class="user-avatar"
            :style="{ backgroundImage: `url(${item.fromAccountImage})` }"
          />
        </div>
      </div>
      <!-- 系统消息 -->
      <div v-if="item.msgType === MSG_ELEMENT_TYPE.NOTICE">
        <div class="system-time">
          <span class="margin-right-10">{{
            noticeTypeName(item.msgOptions.noticeType)
          }}</span>
          {{ formatTime(item.msgTime) }}
        </div>
        <noticeMsg :msgBody="item.msgOptions" :item="item" />
      </div>
      <!-- 两次咨询之间的线条显示 -->
      <div
        v-if="index < currentIMInfoMessages.length - 1"
        type="flex"
        justify="center"
      >
        <el-row
          v-if="
            currentIMInfoMessages[index].fdAsk !=
              currentIMInfoMessages[index + 1].fdAsk
          "
          type="flex"
          justify="center"
          class="line"
        >
          <div class="bottom-line" />
        </el-row>
      </div>
    </li>
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUploadUrlList"
    />
  </ul>
</template>
<script>
import { mapGetters } from 'vuex'
import mywebim from '@/utils/im/mywebim.js' // 本地消息类型解析
import noticeMsg from './noticeMsg'
import chatMsg from './chatMsg'
import Monitor from '@/components/Monitor'
import _ from 'lodash'

export default {
  name: 'ChatAreaList',
  components: {
    Monitor,
    noticeMsg,
    chatMsg
  },
  data() {
    return {
      dialogVisible: false,
      currentMediaType: '',
      currentMediaUrl: '',
      monitorInfo: {},
      MSG_ELEMENT_TYPE: mywebim.MSG_ELEMENT_TYPE,
      currentIMInfoMessages: [],
      msg: Object,
      curUploadUrlList: []
    }
  },
  computed: {
    ...mapGetters(['getCurrentIMInfoMessages', 'userInfo'])
  },
  watch: {
    getCurrentIMInfoMessages(val) {
      this.currentIMInfoMessagesFormat(this.getCurrentIMInfoMessages)
    }
  },
  methods: {
    noticeTypeName(type) {
      let typeName
      switch (type) {
        case 'transfer_apply':
        case 'transfer_accept':
        case 'transfer_reject':
          typeName = '转诊申请'
          break
        case 'round_apply_transfer':
          typeName = '用户转诊意向'
          break
        default:
          typeName = ''
      }
      return typeName
    },
    currentIMInfoMessagesFormat(msgArr) {
      this.curUploadUrlList.length = 0
      this.currentIMInfoMessages = msgArr
      if (msgArr.length === 0) {
        return
      }
      for (let i = 0; i < this.currentIMInfoMessages.length; i++) {
        const item = this.currentIMInfoMessages[i].msgOptions
        if (item.type === 'image' && item.images.oriImage) {
          this.curUploadUrlList.push(item.images.oriImage)
        }
      }
    },
    thumbClickFun(type, url, list) {
      this.monitorInfo = {
        type,
        meta: { url }
      }
      if (list) {
        this.curUploadUrlList = list
      }
      if (
        this.curUploadUrlList &&
        this.curUploadUrlList.length &&
        type !== 'video'
      ) {
        const index = _.findIndex(this.curUploadUrlList, function(o) {
          return o === url
        })
        if (index === -1) {
          this.curUploadUrlList.push(url)
        }
        this.curUploadUrlList = _.sortedUniq(this.curUploadUrlList)
      } else if (type !== 'video') {
        this.curUploadUrlList.push(url)
      } else {
        this.curUploadUrlList = []
      }
      this.$refs.monitor.show()
    }
  }
}
</script>

<style lang="scss" scoped>
.confirm_text {
  font-size: 14px;
  font-weight: bold;
  color: #f98611;
}
ul {
  margin: 0;
  padding: 10px;
  margin-bottom: 70px;
  .el-dialog {
    img {
      width: 100%;
      height: auto;
      text-align: left;
    }
    video {
      width: 100%;
    }
  }
}
.system-time {
  margin-top: 10px;
  line-height: 16px;
  font-size: 12px;
  font-family: PingFangSC-Regular;
  text-align: center;
  color: #576277;
  letter-spacing: 0;
}
.el-row.is-justify-center.el-row--flex.line {
  position: relative;
  margin: 30px auto;
  width: 100%;
}
.bottom-line {
  display: inline-block;
  width: 100%;
  max-width: 450px;
  height: 0;
  text-align: center;
  border-bottom: 1px dotted grey;
}
li {
  position: relative;
  padding: 5px;
  list-style: none;
  margin-bottom: 10px;
  .msg-box {
    max-width: 560px;
    white-space: pre-line;
    word-break: break-all;
  }
  .textLeft {
    float: left;
    .headImage {
      vertical-align: middle;
      .user-avatar {
        display: inline-block;
        margin-right: 20px;
        width: 40px;
        height: 40px;
        background-position: center center;
        background-size: cover;
        border-radius: 50%;
      }
      .time {
        position: absolute;
        top: 29px;
        left: 55px;
        margin-top: -10px;
      }
    }
    .msg-box {
      position: relative;
      .chat-wrapper {
        margin-left: 60px;
        background-color: rgba(255, 255, 255, 0.5);
        img {
          padding: 0;
          max-width: 500px;
          border-radius: 6px;
        }
      }
      .chat-wrapper::before {
        position: absolute;
        top: 8px;
        left: -12px;
        display: block;
        width: 0;
        height: 0;
        border: 6px solid transparent;
        content: '';
        border-right: 6px solid rgba(255, 255, 255, 0.5);
      }
      .chat-wrapper.image_wrapper::before {
        border-right: transparent;
      }
      .chat-wrapper.image_wrapper {
        padding-bottom: 0;
        padding-left: 0;
        background-color: transparent;
        margin-bottom: -8px;
      }
    }
  }
  .textRight {
    float: right;
    .headImage {
      vertical-align: middle;
      .user-avatar {
        position: absolute;
        top: 0;
        right: 0;
        display: inline-block;
        width: 40px;
        height: 40px;
        background-position: center center;
        background-size: cover;
        border-radius: 50%;
      }
      .time {
        position: absolute;
        top: 15px;
        right: 50px;
      }
    }
    .msg-box {
      position: relative;
      .chat-wrapper {
        top: 35px;
        margin-bottom: 20px !important;
      }
      .chat-wrapper::after {
        position: absolute;
        top: 8px;
        right: -12px;
        display: block;
        width: 0;
        height: 0;
        border: 6px solid transparent;
        content: '';
        border-left: 6px solid #c9e7ff;
      }
      .chat-wrapper.image_wrapper::after {
        border-left: 6px solid transparent;
      }
      .chat-wrapper.image_wrapper {
        padding-right: 0;
        padding-bottom: 0;
        background-color: transparent;
        margin-bottom: 10px;
      }
      .msg-status {
        bottom: -12px;
        left: -35px;
      }
    }
  }
  .chat-wrapper {
    position: relative;
    display: inline-block;
    padding: 6px 10px 6px 10px;
    line-height: 28px;
    font-size: 16px; //接诊台消息字体太小调整，以前为12px
    // text-align: right;
    color: #333;
    background-color: #c9e7ff;
    border-radius: 7px;
    z-index: 1;
    img {
      padding: 0;
      max-width: 500px;
      border-radius: 6px;
    }
  }
  .chat-wrapper.image_wrapper {
    background-color: transparent;
  }
  .msg-status {
    position: absolute;
    right: -40px;
    bottom: 3px;
    width: auto;
    min-width: 30px;
    font-size: 12px;
  }
  .read-msg-status {
    color: #999;
  }
  .unread-msg-status {
    color: #0085ff;
  }
  .time {
    font-size: 12px;
    color: #576277;
  }
  .name {
    font-size: 14px;
    color: #17233d;
  }
}
</style>
