<template>
  <div>
    <div class="title-container" @click="boxOpen = !boxOpen">
      <slot name="toggleTitle" />
      <div style="padding-right: 20px">
        <span v-if="showRedCircle" class="red_circle" />
        <i class="el-icon-arrow-right" :class="[boxOpen ? 'open' : 'close']" />
      </div>
    </div>
    <div v-if="boxOpen">
      <slot name="toggleContent" />
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import { mapGetters } from 'vuex'
export default {
  name: 'ToggleBox',
  props: {
    childSessions: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      boxOpen: true,
      showRedCircle: false
    }
  },
  computed: {
    ...mapGetters(['role'])
  },
  watch: {
    childSessions() {
      this.hasUnreadObj()
    }
  },
  mounted() {
    this.hasUnreadObj()
    if (this.role === 'hm') {
      this.boxOpen = true
    } else {
      this.boxOpen = false
    }
  },
  methods: {
    hasUnreadObj() {
      let hasUnreadObj = {}
      if (this.childSessions && this.childSessions.length > 0) {
        hasUnreadObj = _.find(this.childSessions, function(o) {
          return o.un_read_count > 0
        })
      }
      if (hasUnreadObj && Object.keys(hasUnreadObj).length) {
        this.showRedCircle = true
      } else {
        this.showRedCircle = false
      }
    }
  }
}
</script>

<style scoped lang="scss">
.open {
  transition: all 0.5s;
  -ms-transform: rotate(-90deg);

  /* Internet Explorer */
  -moz-transform: rotate(-90deg); /* Firefox */
  -webkit-transform: rotate(-90deg); /* Safari 和 Chrome */
  -o-transform: rotate(-90deg); /* Opera */
  transform: rotate(-90deg);
}
.close {
  transition: all 0.5s;
  -ms-transform: rotate(0deg);

  /* Internet Explorer */
  -moz-transform: rotate(0deg); /* Firefox */
  -webkit-transform: rotate(0deg); /* Safari 和 Chrome */
  -o-transform: rotate(0deg); /* Opera */
  transform: rotate(0deg);
}
.title-container {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
}
.red_circle {
  display: inline-block;
  margin-right: 12px;
  width: 8px;
  height: 8px;
  background: #ff4e4e;
  border-radius: 50%;
}
</style>
