<template>
  <div style="overflow: hidden;line-height: 35px;">
    <div style="padding:10px 15px 0;">
      <el-input
        v-model="cusSearchValue"
        clearable
        placeholder="备注名、编号、昵称等"
        @input="getCustomerTree"
      >
        <i slot="prefix" class="el-input__icon el-icon-search" />
      </el-input>
    </div>
    <scroll-box :box-height="747">
      <slot></slot>
    </scroll-box>
  </div>
</template>

<script>
import _ from 'lodash'
import ScrollBox from '@/components/ScrollBox'
import { mapGetters } from 'vuex'
export default {
  name: 'CustomerList',
  components: {
    ScrollBox
  },
  data() {
    return {
      searchText: ''
    }
  },
  computed: {
    ...mapGetters(['customerTree']),
    cusSearchValue: {
      get() {
        return this.$store.state.chat.cusSearchValue
      },
      set(searchValue) {
        // 使用vuex中的mutations中定义好的方法来改变
        this.$store.commit('chat/SET_CUS_SEARCHVALUE', searchValue)
      }
    }
  },
  methods: {
    // 搜索防抖动
    getCustomerTree: _.debounce(function() {
      this.$store.dispatch('chat/getCustomerTree')
    }, 1000)
  }
}
</script>
<style lang="scss" scoped>
.bg-blue {
  margin-right: 7px;
  width: 29px;
  height: 29px;
  line-height: 30px;
  font-size: 10px;
  text-align: center;
  color: #fff;
  background: #0085ff;
  border-radius: 50%;
}
.group-card {
  user-select: none;
  border-bottom: 1px solid #eee;
  &-title {
    display: flex;
    align-items: center;
    padding: 15px;
    color: #0085ff;
    .group_replace_icon {
      margin-left: 5px;
      font-size: 20px;
    }
    .icon-avatar {
      margin-right: 10px;
      font-size: 30px;
    }
  }
  &-subtitle {
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    cursor: pointer;
    .nurse-name {
      font-size: 16px;
      color: #17233d;
      .replace_icon {
        margin-left: 5px;
        font-size: 18px;
      }
    }
  }
}
</style>
