<template>
  <div clss="msg_type_page">
    <!--文本消息-->
    <p
      v-if="msgType === 'text'"
      class="align-left content"
      @click="goMessage(item)"
      v-html="brightenKeyword(item.content.content, keyword)"
    >
      {{ item.content.content }}
    </p>
    <!--链接消息-->
    <div v-if="msgType === 'link'" class="align-left content">
      <p
        class="link"
        @click="toURL(item.content.content)"
        v-html="brightenKeyword(item.content.content, keyword)"
      />
    </div>
    <!--图片消息-->
    <div v-if="msgType === 'image'" class="image-box" @click="goMessage(item)">
      <img :src="item.content.url" alt="" />
    </div>
    <!--round_msg类型 -->
    <div
      class="card_msg"
      v-if="msgType === 'robot_card' || msgType === 'round_msg'"
    >
      <div
        v-for="(contentItem, ind) in item.content.content"
        :key="contentItem.ask_type ? contentItem.ask_type + ind : ind"
      >
        <BoutCard
          :title="item.content.title"
          :reAsk="item.content.content[0].ask_type === 're_ask'"
          :boutCard="
            boutCardData(contentItem, item.accountType, ind, item.msgOptions)
          "
          :msgTime="item.msgTime"
          @showDetail="goMessage(item)"
          @goMsgDetail="goMessage(item)"
        >
        </BoutCard>
        <p
          v-if="
            ind < item.content.content.length - 1 &&
              item.content.content[0].ask_type !== 're_ask'
          "
          :class="item.isSelfSend ? 'chat_divider' : 'chat_divider_left'"
        ></p>
      </div>
    </div>
    <!--语言消息-->
    <div v-if="msgType === 'voice'" class="image-box" @click="goMessage(item)">
      语音消息，点击查看详情
    </div>
    <!-- 视频消息 -->
    <div v-if="msgType === 'video'" class="image-box" @click="goMessage(item)">
      <div class="VideoContent">
        <img
          :src="
            item.content.url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
          "
          :data-src="
            item.content.url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
          "
          alt=""
        />
        <i class="el-icon-video-play" />
      </div>
    </div>
    <!-- 电话录音消息 -->
    <div
      v-if="msgType === 'voicecard' && item.content.url"
      class="image-box"
      @click="goMessage(item)"
    >
      <p>录音消息，点击查看详情</p>
    </div>
    <div v-if="msgType === 'voicecard' && !item.content.url" class="image-box">
      <p>录音待上传，请稍后查看</p>
    </div>
    <!--卡片消息-->
    <div v-if="msgType === 'card'" class="image-box" @click="goMessage(item)">
      <p>卡片消息，点击查看详情</p>
    </div>
    <!--系统消息-->
    <div
      v-if="
        msgType === 'notice' ||
          msgType === 'suggestion' ||
          msgType === 'comment'
      "
      class="align-left"
      @click="goMessage(item)"
    >
      <SystemContent
        v-if="item.content.notice_type === 'transfer_accept'"
        style="text-align:left;"
        :msg-type="item.show_in_admin ? 'systemWarnning' : 'systemInfo'"
        :content="item.content.content"
        v-html="
          brightenKeyword(item.content.content || item.content.title, keyword)
        "
      />
      <div v-else>
        <div class="image-box" @click="goMessage(item)">
          系统消息，点击查看详情
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BoutCard from '@/views/medical-service/diagnose/chat/chatArea/components/BoutCard'
import SystemContent from '@/views/medical-service/diagnose/chat/chatArea/components/SystemContent'
import { boutCardData } from '@/filters'
import { brightenKeyword } from '@/utils/index.js'

export default {
  components: {
    SystemContent,
    BoutCard
  },
  props: {
    msgType: {
      type: String,
      default: ''
    },
    keyword: {
      type: String,
      default: ''
    },
    item: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {}
  },
  methods: {
    brightenKeyword,
    boutCardData,
    goMessage(msgObj) {
      this.$emit('goMsg', msgObj)
    },
    toURL(val) {
      if (val) {
        window.open(val)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.msg_type_page {
  font-size: 12px;
}
.link {
  padding: 10px 15px;
  border-top: 1px solid #e8eaec !important;
  .content {
    padding: 10px;
    font-size: 12px;
    text-align: justify;
    background: #f6f7fb;
    .content-title {
      line-height: 12px;
      color: #576277;
      margin-bottom: 5px;
    }
    .link {
      line-height: 16px;
      color: #0085ff;
    }
  }
  &:last-child {
    border-bottom: 1px solid #e8eaec !important;
  }
}
.VideoContent {
  position: relative;
  max-width: 350px;
  img {
    max-width: 290px;
    max-height: 260px;
  }
  i.el-icon-video-play {
    position: absolute;
    top: 30%;
    left: 31.5%;
    font-size: 35px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}
.image-box {
  position: relative;
  float: left;
  text-align: left;
  img {
    width: 100px;
    height: 100px;
  }
}
</style>
