<template>
  <div class="SendImage">
    <el-tooltip
      :content="!hasServer ? '该用户套餐不包含此服务' : '新建健康随访任务'"
      placement="top"
    >
      <svg-icon
        icon-class="health_follow"
        :class="!hasServer ? 'notAllow' : ''"
        @click="openDrawer"
      />
    </el-tooltip>
    <el-drawer
      title="新建健康随访任务"
      size="25%"
      :modal="false"
      :visible.sync="drawer"
      direction="ltr"
      @opened="resetForm"
    >
      <div class="drawer-content">
        <div class="drawer-item">
          <div class="label">当前用户：</div>
          <div class="name">{{ customerName }}</div>
        </div>
        <div class="drawer-item">
          <div class="label">患者：</div>
          <div class="name">
            {{ babyName }}
          </div>
        </div>
        <el-form ref="ruleForm" :model="ruleForm" label-width="120px">
          <el-form-item
            label="标题："
            prop="title"
            :rules="[
              { required: true, message: '请填写标题', trigger: 'blur' }
            ]"
          >
            <div>
              <el-input
                v-model="ruleForm.title"
                maxlength="20"
                show-word-limit
              />
            </div>
          </el-form-item>
          <el-form-item label="备注：" prop="remark">
            <el-input
              v-model="ruleForm.remark"
              type="textarea"
              maxlength="100"
              show-word-limit
              rows="5"
            />
          </el-form-item>
          <el-form-item
            prop="executeTime"
            :rules="[
              { required: true, message: '请选择任务执行日期', trigger: 'blur' }
            ]"
          >
            <span slot="label">
              任务执行日期
            </span>
            <div>
              <el-date-picker
                v-model="ruleForm.executeTime"
                type="date"
                placeholder="请选择任务执行日期"
                :picker-options="pickerOptions"
                style="width: 100%;"
              />
            </div>
          </el-form-item>
          <el-form-item>
            <div class="button-container">
              <el-button type="primary" @click="createTask">确认创建</el-button>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { createFollowTask } from '@/api/obstetricsFollow'
export default {
  name: 'chankeHealthFollow',
  props: {
    hasServer: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 1000 * 60 * 60 * 24
        }
      },
      uploading: false,
      drawer: false,
      ruleForm: {
        title: '',
        remark: '',
        executeTime: ''
      }
    }
  },
  computed: {
    ...mapGetters(['activedBaby', 'activedBabyId', 'activedUserInfo']),
    babyName() {
      const babyName =
        this.activedBaby && this.activedBaby.family_member
          ? this.activedBaby.family_member.name
          : ''
      return babyName
    },
    customerName() {
      const currentuser = this.activedUserInfo
      return currentuser.name
    }
  },
  methods: {
    resetForm() {
      this.$refs.ruleForm.resetFields()
    },
    openDrawer() {
      if (!this.hasServer) {
        return
      }
      this.drawer = true
    },
    closeDrawer() {
      setTimeout(() => {
        this.drawer = false
      }, 1000)
    },
    createTask() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          const { title, remark, executeTime } = this.ruleForm
          const data = {
            family_member: this.activedBabyId,
            title,
            remark,
            task_type: 'health_follow',
            task_message_type: 'create_task',
            executed_at: executeTime
          }
          // 新建健康随访任务
          createFollowTask(data).then(res => {
            if (res.data.result === 20000) {
              this.closeDrawer()
            }
            this.$message.success('创建成功')
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.drawer-content {
  padding-top: 30px;
  padding-right: 20px;
  .drawer-item {
    display: flex;
    margin-left: 25px;
    line-height: 50px;
    font-size: 14px;
    color: #17233d;
    .label {
      width: 84px;
      text-align: right;
      // flex: 1;
    }
    .name {
      margin-left: 20px;
      flex: 4;
    }
  }
  .button-container {
    text-align: right;
  }
}
</style>
