<template>
  <div class="grow-data">
    <el-tooltip :content="'宝宝生长数据'" placement="top">
      <el-badge>
        <svg-icon icon-class="bbszshuju" @click="openDrawer" />
      </el-badge>
    </el-tooltip>
    <el-drawer
      title="宝宝生长数据"
      size="32%"
      :visible.sync="drawer"
      direction="rtl"
      class="drawer-content"
      :modal="false"
    >
      <div class="drawer-content">
        <div style="padding: 30px;">
          <div class="baby-info-container">
            <div class="baby">
              <div class="label">
                宝宝:
              </div>
              <div class="name">
                {{ babyName }}
              </div>
            </div>
            <div class="user">
              <div class="label">
                当前用户:
              </div>
              <div class="name">
                {{ userName }}
              </div>
            </div>
          </div>
          <div class="button-container">
            <div>
              <el-button
                :disabled="!operateData"
                type="text"
                @click="handleAdd"
              >
                添加一条生长数据
              </el-button>
            </div>
            <div>
              <el-button
                type="text"
                :disabled="!operateData"
                @click="handleEdit"
              >
                修改
              </el-button>
              <el-button
                type="text"
                :disabled="!operateData"
                @click="handleDelete"
              >
                删除
              </el-button>
            </div>
          </div>
          <el-table
            :data="tableData"
            height="750px"
            :header-cell-style="{ background: '#F5F7FA' }"
          >
            <el-table-column
              header-align="center"
              align="center"
              label="宝宝月龄"
              width="180"
            >
              <template slot-scope="scope">
                <div style="display: flex">
                  <div>
                    <el-radio
                      v-model="selectRadio"
                      :label="scope.row._id"
                      @change.native="getTemplateRow(scope.$index, scope.row)"
                      >&nbsp;</el-radio
                    >
                  </div>
                  <div style="width: 100px">
                    {{ scope.row.year }} 岁 {{ scope.row.month }} 月
                    {{ scope.row.day }} 天
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              label="身高/身长（cm）百分位（%）"
              width="140"
            >
              <template slot-scope="scope">
                <div>
                  <span v-html="makePlaceHolder(scope.row.height, true)">
                  </span>
                  &nbsp;cm<br />
                  <span v-if="scope.row.height_type === 0">
                    (<span
                      v-html="
                        makePlaceHolder(scope.row.height_from_percent, false)
                      "
                    >
                    </span
                    >% 到
                    <span
                      v-html="
                        makePlaceHolder(scope.row.height_to_percent, false)
                      "
                    >
                    </span>
                    %)
                  </span>
                  <span v-else>
                    (
                    <span
                      v-html="makePlaceHolder(scope.row.height_percent, false)"
                    >
                    </span>
                    %)
                  </span>
                </div>
              </template>
            </el-table-column>
            <el-table-column header-align="center" align="center" width="120">
              <template slot="header">
                <div>
                  <div>体重（kg）</div>
                  <div>百分位（%）</div>
                </div>
              </template>
              <template slot-scope="scope">
                <div>
                  <span v-html="makePlaceHolder(scope.row.weight, true)">
                  </span>
                  &nbsp;kg<br />
                  <span v-if="scope.row.weight_type === 0">
                    (<span
                      v-html="
                        makePlaceHolder(scope.row.weight_from_percent, false)
                      "
                    ></span
                    >% 到
                    <span
                      v-html="
                        makePlaceHolder(scope.row.weight_to_percent, false)
                      "
                    ></span
                    >%)
                  </span>
                  <span v-else>
                    (<span
                      v-html="makePlaceHolder(scope.row.weight_percent, false)"
                    ></span
                    >%)
                  </span>
                </div>
              </template>
            </el-table-column>
            <el-table-column header-align="center" align="center" width="120">
              <template slot="header">
                <div>
                  <div>头围（cm）</div>
                  <div>百分位（%）</div>
                </div>
              </template>
              <template slot-scope="scope">
                <div>
                  <span v-html="makePlaceHolder(scope.row.head, true)"> </span
                  >&nbsp;cm<br />
                  <span v-if="scope.row.head_type === 0">
                    (<span
                      v-html="
                        makePlaceHolder(scope.row.head_from_percent, false)
                      "
                    ></span
                    >% 到
                    <span
                      v-html="makePlaceHolder(scope.row.head_to_percent, false)"
                    ></span
                    >%)
                  </span>
                  <span v-else>
                    (<span
                      v-html="makePlaceHolder(scope.row.head_percent, false)"
                    ></span
                    >%)
                  </span>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </el-drawer>
    <el-dialog title="未选中生长数据" :visible.sync="dialogVisible" width="25%">
      <span>请先选中某个月龄的一条生长数据再进行修改或删除操作</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >返回选择</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      title="删除宝宝生长数据"
      :visible.sync="deleteConfirm"
      width="30%"
    >
      <span>删除后该条数据将从宝宝生长数据列表中移除，请确认是否继续删除</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="deleteGrowthData">删除</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :title="
        addOrEditStatus === 'add' ? '添加宝宝生长数据' : '修改宝宝生长数据'
      "
      :visible.sync="addOrEditVisible"
      width="40%"
      @opened="resetRuleForm"
    >
      <el-form
        ref="ruleForm"
        :model="ruleForm"
        label-width="100px"
        :label-position="'left'"
        class="demo-ruleForm"
      >
        <el-form-item
          label="宝宝月龄"
          prop="year"
          :rules="[
            { required: true, message: '请输入宝宝月龄', trigger: 'blur' },
            { validator: checkTaskTime, trigger: 'blur' }
          ]"
        >
          <div class="age-flex-box">
            <div class="age-container">
              <el-input
                v-model.number="ruleForm.year"
                placeholder="请填写自然数"
                size="mini"
                @input="setNumber($event, 'year')"
              />&nbsp;&nbsp;岁
            </div>
            <div class="age-container">
              <el-input
                ref="month"
                v-model.number="ruleForm.month"
                placeholder="请填写0-12的整数"
                @input="setNumber($event, 'month')"
                size="mini"
              />&nbsp;&nbsp;月
            </div>
            <div class="age-container">
              <el-input
                ref="day"
                v-model.number="ruleForm.day"
                placeholder="请填写0-31的整数"
                @input="setNumber($event, 'day')"
                size="mini"
              />&nbsp;&nbsp;天
            </div>
          </div>
        </el-form-item>
        <el-form-item label="身高/身长" prop="height">
          <div class="age-flex-box">
            <div class="age-container">
              <el-input
                v-model="ruleForm.height"
                placeholder="请输入"
                size="mini"
                @input="setNumber($event, 'sc')"
                @blur="toFixedNum($event, 'sc')"
                @change="toFixedNum($event, 'sc')"
              />&nbsp;&nbsp;(cm)
            </div>
            <div class="range-select">
              &nbsp;&nbsp;&nbsp;&nbsp;
              <el-select
                v-model="ruleForm.height_type"
                placeholder="请选择"
                size="mini"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </div>
            <div v-if="ruleForm.height_type === 0" class="range-container">
              <div class="range-item">
                百分位&nbsp;&nbsp;
                <el-input
                  ref="heightFromPercent"
                  v-model.number="ruleForm.height_from_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'scpf')"
                />
              </div>
              <div class="range-item" style="margin-left: -5px">
                %到&nbsp;&nbsp;
                <el-input
                  ref="heightToPercent"
                  v-model.number="ruleForm.height_to_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'scpt')"
                />&nbsp;&nbsp;%
              </div>
            </div>
            <div v-else class="value-container">
              <div class="value-item">
                百分位&nbsp;&nbsp;
                <el-input
                  ref="heightPercent"
                  v-model.number="ruleForm.height_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'scp')"
                />&nbsp;&nbsp;%
              </div>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="体重" prop="weight">
          <div class="age-flex-box">
            <div class="age-container">
              <el-input
                v-model="ruleForm.weight"
                placeholder="请输入"
                size="mini"
                @input="setNumber($event, 'tz')"
                @blur="toFixedNum($event, 'tz')"
                @change="toFixedNum($event, 'tz')"
              />&nbsp;&nbsp;&nbsp;(kg)
            </div>
            <div class="range-select">
              &nbsp;&nbsp;&nbsp;&nbsp;
              <el-select
                v-model="ruleForm.weight_type"
                placeholder="请选择"
                size="mini"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </div>
            <div v-if="ruleForm.weight_type === 0" class="range-container">
              <div class="range-item">
                百分位&nbsp;&nbsp;
                <el-input
                  ref="weightFromPercent"
                  v-model.number="ruleForm.weight_from_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'tzpf')"
                />
              </div>
              <div class="range-item" style="margin-left: -5px">
                %到&nbsp;&nbsp;
                <el-input
                  ref="weightToPercent"
                  v-model.number="ruleForm.weight_to_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'tzpt')"
                />&nbsp;&nbsp;%
              </div>
            </div>
            <div v-else class="value-container">
              <div class="value-item">
                百分位&nbsp;&nbsp;
                <el-input
                  ref="weightPercent"
                  v-model.number="ruleForm.weight_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'tzp')"
                />&nbsp;&nbsp;%
              </div>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="头围" prop="head">
          <div class="age-flex-box">
            <div class="age-container">
              <el-input
                v-model="ruleForm.head"
                placeholder="请输入"
                size="mini"
                @input="setNumber($event, 'tw')"
                @blur="toFixedNum($event, 'tw')"
                @change="toFixedNum($event, 'tw')"
              />&nbsp;&nbsp;(cm)
            </div>
            <div class="range-select">
              &nbsp;&nbsp;&nbsp;&nbsp;
              <el-select
                v-model="ruleForm.head_type"
                placeholder="请选择"
                size="mini"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </div>
            <div v-if="ruleForm.head_type === 0" class="range-container">
              <div class="range-item">
                百分位&nbsp;&nbsp;
                <el-input
                  ref="headFromPercent"
                  v-model.number="ruleForm.head_from_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'twpf')"
                />
              </div>
              <div class="range-item" style="margin-left: -5px">
                %到&nbsp;&nbsp;
                <el-input
                  ref="headToPercent"
                  v-model.number="ruleForm.head_to_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'twpt')"
                />&nbsp;&nbsp;%
              </div>
            </div>
            <div v-else class="value-container">
              <div class="value-item">
                百分位&nbsp;&nbsp;
                <el-input
                  ref="headPercent"
                  v-model.number="ruleForm.head_percent"
                  placeholder="请输入"
                  size="mini"
                  @input="setPercent($event, 'twp')"
                />&nbsp;&nbsp;%
              </div>
            </div>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <div class="align-center">
          <el-button type="primary" @click="confirmAddOrEdit">{{
            addOrEditStatus === 'add' ? '确认添加' : '确认修改'
          }}</el-button>
        </div>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
import {
  createGrowthInfo,
  getGrowthList,
  deleteGrowthInfo,
  updateGrowthInfo
} from '@/api/babyGrowth'
export default {
  components: {},
  props: {
    babyId: {
      type: String,
      default: ''
    },
    hasServer: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      babyName: '',
      userName: '',
      addOrEditStatus: 'add',
      options: [
        {
          value: 0,
          label: '范围'
        },
        {
          value: 1,
          label: '值'
        }
      ],
      ruleForm: {
        year: '',
        month: '',
        day: '',
        height: '',
        height_type: 0,
        height_from_percent: '',
        height_to_percent: '',
        height_percent: '',
        weight: '',
        weight_type: 0,
        weight_from_percent: '',
        weight_to_percent: '',
        weight_percent: '',
        head: '',
        head_type: 0,
        head_from_percent: '',
        head_to_percent: '',
        head_percent: ''
      },
      deleteConfirm: false,
      dialogVisible: false,
      addOrEditVisible: false,
      drawer: false,
      selectRadio: null,
      tableData: []
    }
  },
  computed: {
    operateData() {
      return this.$store.state.user.bus_role === 'hm'
    }
  },
  watch: {},
  mounted() {},
  methods: {
    makePlaceHolder(value, needToFixed) {
      if (needToFixed) {
        return value ? value.toFixed(2) : "<span style='color: red'>-</span>"
      } else {
        return value
          ? value
          : "<span style='font-size: 8px;color: red'> - </span>"
      }
    },
    setNumber(value, type) {
      if (type === 'tz') if (!Number(value)) this.ruleForm.weight = ''
      if (type === 'sc') if (!Number(value)) this.ruleForm.height = ''
      if (type === 'tw') if (!Number(value)) this.ruleForm.head = ''
      if (type === 'year')
        if (!Number(value) && Number(value) !== 0) this.ruleForm.year = ''
      if (type === 'month')
        if (!Number(value) && Number(value) !== 0) this.ruleForm.month = ''
      if (type === 'day')
        if (!Number(value) && Number(value) !== 0) this.ruleForm.day = ''
    },
    setPercent(value, type) {
      if (type === 'scpf')
        if (!Number(value)) this.ruleForm.height_from_percent = ''
      if (type === 'scpt')
        if (!Number(value)) this.ruleForm.height_to_percent = ''
      if (type === 'scp') if (!Number(value)) this.ruleForm.height_percent = ''
      if (type === 'tzpf')
        if (!Number(value)) this.ruleForm.weight_from_percent = ''
      if (type === 'tzpt')
        if (!Number(value)) this.ruleForm.weight_to_percent = ''
      if (type === 'tzp') if (!Number(value)) this.ruleForm.weight_percent = ''
      if (type === 'twpf')
        if (!Number(value)) this.ruleForm.head_from_percent = ''
      if (type === 'twpt')
        if (!Number(value)) this.ruleForm.head_to_percent = ''
      if (type === 'twp') if (!Number(value)) this.ruleForm.head_percent = ''
    },
    toFixedNum(value, type) {
      if (!value) return
      if (type === 'tz')
        if (Number(value)) this.ruleForm.weight = Number(value).toFixed(2)
      if (type === 'sc')
        if (Number(value)) this.ruleForm.height = Number(value).toFixed(2)
      if (type === 'tw')
        if (Number(value)) this.ruleForm.head = Number(value).toFixed(2)
    },
    // 模态框打开时重置表单验证
    resetRuleForm() {
      this.$refs.ruleForm.clearValidate()
    },
    getGrowthList() {
      getGrowthList(this.babyId).then(res => {
        if (res.code === 20000) {
          const data = res.data
          this.tableData = data.list
          this.babyName = _.get(data, 'baby.name')
          this.userName = _.get(data, 'baby.user.nickname')
        }
      })
    },
    checkTaskTime(rule, value, callback) {
      const month = this.$refs.month.value
      const day = this.$refs.day.value
      if (value === '' || month === '' || day === '') {
        callback(new Error('请填写全部宝宝月龄'))
      }
      if (month > 12) {
        callback(new Error('任务月龄月数不能大于12'))
      }
      if (day > 31) {
        callback(new Error('任务月龄天数不能大于31'))
      }
      callback()
    },
    // 确认新增或修改
    confirmAddOrEdit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (this.addOrEditStatus === 'add') {
            // 新增
            this.ruleForm.family_member = this.babyId
            createGrowthInfo(this.ruleForm).then(res => {
              if (res.code === 20000) {
                this.$message.success('新增成功')
                this.addOrEditVisible = false
                this.getGrowthList()
                this.selectRadio = null
              }
            })
          } else {
            // 修改
            updateGrowthInfo(this.selectRadio, this.ruleForm).then(res => {
              if (res.code === 20000) {
                this.$message.success('修改成功')
                this.addOrEditVisible = false
                this.getGrowthList()
                this.selectRadio = null
              }
            })
          }
        } else {
          return false
        }
      })
    },
    // 删除生长数据
    deleteGrowthData() {
      deleteGrowthInfo(this.selectRadio).then(res => {
        if (res.code === 20000) {
          this.$message.success('删除成功')
          this.deleteConfirm = false
          this.getGrowthList()
          this.selectRadio = null
        }
      })
    },
    // 添加一条生长数据
    handleAdd() {
      this.addOrEditStatus = 'add'
      this.selectRadio = null
      this.ruleForm = {
        year: '',
        month: '',
        day: '',
        height: '',
        height_type: 0,
        height_from_percent: '',
        height_to_percent: '',
        height_percent: '',
        weight: '',
        weight_type: 0,
        weight_from_percent: '',
        weight_to_percent: '',
        weight_percent: '',
        head: '',
        head_type: 0,
        head_from_percent: '',
        head_to_percent: '',
        head_percent: ''
      }
      this.addOrEditVisible = true
    },
    // 编辑生长数据
    handleEdit() {
      if (!this.selectRadio) {
        this.dialogVisible = true
        return
      }
      // 打开编辑弹窗
      this.addOrEditStatus = 'edit'
      this.addOrEditVisible = true
    },
    // 删除生长数据
    handleDelete() {
      if (!this.selectRadio) {
        this.dialogVisible = true
        return
      }
      this.deleteConfirm = true
      // 打开删除确认选择弹窗
    },
    getTemplateRow(index, row) {
      this.ruleForm = _.cloneDeep(row)
      this.ruleForm = _.pick(this.ruleForm, [
        'year',
        'month',
        'day',
        'height',
        'height_type',
        'height_from_percent',
        'height_to_percent',
        'height_percent',
        'weight',
        'weight_type',
        'weight_from_percent',
        'weight_to_percent',
        'weight_percent',
        'head',
        'head_type',
        'head_from_percent',
        'head_to_percent',
        'head_percent'
      ])
    },
    openDrawer() {
      // 清空表格单选状态
      this.selectRadio = null
      this.drawer = true
      this.getGrowthList()
    }
  }
}
</script>
<style lang="scss" scoped>
.grow-data {
  line-height: 29px;
}
.age-flex-box {
  display: flex;
  .age-container {
    flex: 3;
  }
  .range-select {
    flex: 2;
    display: flex;
  }
  .range-container {
    display: flex;
    margin-left: -20px;
    flex: 5;
    .range-item {
      margin-left: 30px;
      flex: 1;
    }
  }
  .value-container {
    position: relative;
    left: 40px;
    flex: 4;
    display: flex;
  }
}
.drawer-content {
  .baby-info-container {
    display: flex;
    width: 100%;
    color: #17233d;
    .label {
      font-weight: bold;
    }
    .name {
      margin-left: 30px;
    }
    .baby {
      display: flex;
      width: 200px;
    }
    .user {
      display: flex;
      width: 300px;
    }
  }
  .button-container {
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
  }
}
</style>
<style lang="scss">
.grow-data {
  .el-drawer {
    .el-drawer__header {
      font-weight: 600;
      text-align: center;
      color: #17233d;
    }
  }
}

.age-container {
  .el-input {
    width: 80%;
  }
}
.range-container {
  .el-input {
    width: 55%;
  }
}
.value-container {
  .el-input {
    width: 65%;
  }
}
.range-select {
  .el-input {
    width: 100%;
  }
}
</style>
