<template>
  <div class="bout-input-box">
    <p class="tip-content">
      温馨提示：该用户套餐中限制了每次咨询的提问次数，不限制回复次数，需要您单击【回复完毕】按钮以后，用户才可以继续提问！
    </p>
    <div
      v-for="(item, index) in answerList"
      :key="'answerList' + index"
      class="answer"
    >
      <div class="textarea_wrap">
        <textarea v-model="item.content" type="textarea" autofocus rows="4">
        </textarea>
        <span class="limit_box"> {{ currentLength }}/{{ maxLength }} </span>
      </div>
      <el-row>
        <el-col :span="22">
          <UploaderList
            v-model="item.media"
            :max="9"
            :width="'50px'"
            :height="'50px'"
          />
        </el-col>
      </el-row>
    </div>
    <el-row>
      <el-col
        :span="8"
        class="count-box align-left"
        :class="hasTransfer ? '' : 'no-transfer'"
      >
        <div v-if="hasTransfer">
          <span v-if="lastRound.transfer">
            本次转诊回合数
            <span class="span_val">
              {{ lastRound.transfer.round_num }}/{{
                lastRound.transfer.round_limit
              }}
            </span>
            <el-tooltip content="当前回合/回合数上限" placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
          </span>
        </div>
        <div>
          <span v-if="lastRound && lastRound.ask">
            本次咨询回合数
            <span class="span_val"
              >{{ lastRound.ask.round_num }}/{{
                lastRound.ask.round_limit
              }}</span
            >
            <el-tooltip content="当前回合/回合数上限" placement="top">
              <i class="el-icon-info"></i>
            </el-tooltip>
          </span>
        </div>
      </el-col>
      <el-col :span="16" class="align-right">
        <el-button
          size="mini"
          type="primary"
          @click="replyEndFun"
          :disabled="replyEndBtnDiabeled"
          >回复完毕</el-button
        >
        <el-button
          size="mini"
          type="primary"
          @click="showAddBoutDialog"
          :disabled="addBtnIsAppend"
          >追加回合</el-button
        >
        <el-button
          size="mini"
          type="primary"
          class="close-btn"
          :disabled="closeBtnDisabled"
          plain
          @click="closeBoutConsult"
        >
          结束咨询
        </el-button>
        <el-button
          size="mini"
          :disabled="!previewBtnStatus"
          type="primary"
          :loading="repyLoading"
          @click="preview"
          >发送</el-button
        >
      </el-col>
    </el-row>
    <AddboutDialog
      :addBoutVisible="addBoutVisible"
      @addBoutConfirm="addBoutConfirm"
      @addBoutCancel="addBoutCancel"
    ></AddboutDialog>
  </div>
</template>
<script>
import UploaderList from '@/components/UploaderList'
import AddboutDialog from './components/AddBoutDialog'
import { addBoutApi } from '@/api/bout'
import { handleMessageSend, replyEndApi } from '@/api/message'
import { mapGetters } from 'vuex'
import { localSave, localRead } from '@/utils/index.js'

export default {
  components: {
    UploaderList,
    AddboutDialog
  },
  props: {
    closeBtnStatus: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapGetters([
      'lastAsk',
      'askMode',
      'activedBaby',
      'activedBabyId',
      'getReplaceStatus',
      'role'
    ])
  },
  data() {
    return {
      currentLength: 0,
      maxLength: 2000,
      addBoutVisible: false,
      addBtnIsAppend: true, //追加回合按钮不可用
      repyLoading: false,
      previewBtnStatus: false,
      replyEndBtnDiabeled: true, // 回复完毕按钮是否可用
      hasTransfer: false, //是否在转诊中
      closeBtnDisabled: true,
      hasAsking: false, //是否在咨询中
      lastRound: {},
      answerList: [
        {
          content: '',
          media: []
        }
      ]
    }
  },
  watch: {
    lastAsk: {
      handler: function(val) {
        if (val) {
          this.initPage()
        }
      },
      deep: true
    },
    answerList: {
      handler: function(val) {
        // 缓存数据
        localSave(this.activedBabyId, JSON.stringify(val))
        // maxlength判断
        this.currentLength = val[0].content.length
        if (this.currentLength >= 2000) {
          this.currentLength = 2000
          this.answerList[0].content = val[0].content.substring(0, 2000)
        }
      },
      deep: true
    },
    getReplaceStatus() {
      // 代班状态改变，更新按钮权限
      this.getBtnStatus()
    }
  },
  created() {
    this.initPage()
  },
  methods: {
    replyEndFun() {
      // pending_reply in_replay stop_replay 待回复、回复中、已回复
      if (this.lastRound.reply_status === 'pending_reply') {
        this.$message.warning('请先回复用户')
        return
      }
      //回复完毕
      replyEndApi(this.lastAsk._id).then(res => {
        this.$store.dispatch('chat/getLastAsk') //获取预览按钮的状态
      })
    },
    initPage() {
      if (this.lastAsk) {
        this.lastRound = this.lastAsk.round
        this.hasAsking =
          this.lastAsk.status && this.lastAsk.status !== 'ask_end'
        // 是否在转诊中
        this.hasTransfer =
          (this.lastAsk.has_transfer &&
            this.lastAsk.transfer_status === 'start') ||
          this.lastAsk.transfer_status === 'asking'
      }
      this.getCacheData() //获取缓存的数据
      this.getBtnStatus() //获取预览按钮的状态
    },
    getBtnStatus() {
      // 在咨询中
      if (this.hasAsking) {
        this.closeBtnDisabled = !(
          this.lastRound &&
          this.lastRound.is_append &&
          this.lastRound.can_append
        )
        if (this.hasTransfer) {
          if (this.role === this.lastAsk.transfer_role) {
            this.previewBtnStatus = this.lastRound.can_reply
            this.addBtnIsAppend = this.lastRound.is_append
          } else {
            this.previewBtnStatus = false
            this.addBtnIsAppend = true
          }
          // 当前咨询中
        } else {
          // 且当前用户角色为hm则取后台返回的当前状态
          if (this.role === 'hm') {
            this.previewBtnStatus = this.lastRound.can_reply
            this.addBtnIsAppend = this.lastRound.is_append
          } else {
            // 否则禁用
            this.previewBtnStatus = false
            this.closeBtnDisabled = true
            this.addBtnIsAppend = true
          }
        }
        // 代班状态，禁止发送按钮
        if (
          this.getReplaceStatus.replace &&
          this.getReplaceStatus.replace.status === 'replace_by_other'
        ) {
          this.previewBtnStatus = false
        }
        // 回复完毕按钮状态：发送按钮可用且当前回合制咨询状态没有结束回复时，按钮可用，否则按钮不可用
        this.replyEndBtnDiabeled = !(
          this.previewBtnStatus && this.lastRound.reply_status !== 'stop_reply'
        )
      } else {
        // 禁用
        this.replyEndBtnDiabeled = true
        this.closeBtnDisabled = true
        this.previewBtnStatus = false
        this.addBtnIsAppend = true
      }
    },
    getCacheData() {
      // 加载页面时，读取浏览器缓存到回答列表
      this.$nextTick(() => {
        const storageAnswer = localRead(this.activedBabyId)
        if (this.activedBabyId && storageAnswer) {
          this.answerList = JSON.parse(storageAnswer)
        } else {
          this.resetAnswerList()
        }
      })
    },
    closeBoutConsult() {
      this.$emit('closeBoutConsult', 'bout')
    },
    addBoutCancel() {
      this.addBoutVisible = false
    },
    showAddBoutDialog() {
      this.addBoutVisible = true
    },
    async addBoutConfirm(val) {
      // 确认添加回合
      const { message, data } = await addBoutApi(this.lastAsk._id, {
        reason: val
      })
      this.$message({
        type: data.result ? 'success' : 'error',
        message: data.result ? '追加回合成功' : message
      })
      if (data.result) {
        this.addBoutCancel()
      }
    },
    resetAnswerList() {
      this.answerList = [
        {
          content: '',
          media: []
        }
      ]
    },
    preview() {
      // 点击预览按钮进行预览,预览时,输入框必须有值
      const emptyConent = this.answerList.findIndex(i => !i.content)
      if (emptyConent !== -1 || this.answerList[0].content.trim() === '') {
        this.$message.warning('未检测到您输入的诊断内容，请输入后进行发送！')
      } else {
        this.confirmReply()
      }
    },
    async confirmReply() {
      // 点击确认回复清空浏览器缓存
      localStorage.removeItem(this.activedBabyId)
      const params = {
        fd_ask: this.lastAsk._id ? this.lastAsk._id : '', // 咨询id,
        message_body: {
          // 消息体
          type: 'round_msg',
          content: this.answerList
        }
      }
      this.repyLoading = true
      this.$store.dispatch('setTrigger', 'm', { root: true })
      await handleMessageSend(params)
        .then(res => {
          this.repyLoading = false
          if (res.data.result) {
            this.$store.dispatch('pushCurrentIMInfoMessages', res.data.message)
            this.$store.dispatch('setBottomBtn', false, { root: true }) // 隐藏直达最新消息按钮
            this.resetAnswerList()
          } else {
            this.$message.error(res.data.message)
          }
        })
        .catch(error => {
          this.repyLoading = false
          console.log(error)
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.answer {
  .textarea_wrap {
    position: relative;
    color: #909399;
    textarea {
      padding: 12px 12px 16px;
      width: 100%;
      line-height: 20px;
      color: #606266;
      background: #fff;
      border: 1px solid #dcdfe6;
      border-radius: 4px;
      outline: none;
      resize: none;
      letter-spacing: 0;
      word-break: break-all;
    }
    .limit_box {
      position: absolute;
      right: 20px;
      bottom: 1px;
      font-size: 10px;
    }
  }
}
.bout-input-box {
  overflow-y: auto;
  padding: 10px;
  width: 100%;
  height: 100%;
  max-height: 400px;
  background: #f5f7fa;
  .span_val {
    padding: 1px 2px;
    color: rgba(255, 183, 24, 1);
    background: rgba(255, 255, 255, 0.77);
  }
  .close-btn {
    padding: 6px 12px;
    color: #409eff;
    border: 1px solid #409eff !important;
    &:hover,
    &:focus {
      color: #409eff;
      background: #ecf5ff;
    }
  }
  .count-box {
    font-size: 12px;
    color: #576277;
  }
  .no-transfer {
    margin-top: 7px;
  }
  .awite-time {
    .time-style {
      margin-left: 4px;
      padding: 0 2px;
      line-height: 14px;
      font-size: 14px;
      font-family: PingFangSC-Regular;
      color: #ffb718;
      background: #fff;
      border-radius: 2px;
      letter-spacing: 0;
    }
    .time-unit {
      margin-left: 4px;
    }
  }
  .tip-title {
    font-size: 20px;
    margin-bottom: 14px;
  }
  .tip-content {
    line-height: 16px;
    font-size: 14px;
    color: #576277;
    margin-bottom: 14px;
  }
  .btn--icon {
    margin-top: 18px;
    font-size: 30px;
    cursor: pointer;
  }
  .answer + .answer {
    position: relative;
    margin-top: 20px;
  }
  .close_question_icon {
    position: absolute;
    top: -6px;
    right: -3px;
    color: #fff;
    background: #808695;
    border-radius: 50px;
    z-index: 20;
    cursor: pointer;
  }
}
</style>
