<template>
  <div class="clearfix">
    <div class="float-left">
      <slot name="image"></slot>
      <slot name="video"></slot>
      <slot name="fastReply"></slot>
      <!-- 转诊 健康管理师可见或产科的医生 TODOS: -->
      <transfer-diagnose
        v-if="role === 'hm'"
        class="float-left"
        :has-server="ifHasServer('transfer')"
      />
    </div>
    <div class="float-right">
      <!-- 聊天记录 -->
      <MsgHistory class="float-right" />
      <!-- 新建健康管理 -->
      <health-manage
        v-if="bus_role === 'hm' && department === 'erke'"
        class="float-right"
        :has-server="ifHasServer('health_manager')"
      />
      <!-- 新建健康随访任务 TODOS:产科医生可操作 -->
      <health-Follow
        v-if="
          department === 'chanke' &&
            bus_role === 'doctor' &&
            userInfo.fd_info.department === 'obstetrics'
        "
        class="float-right"
        :has-server="ifHasServer('health_follow')"
      />
      <!-- 新建定制食谱 -->
      <recipes
        v-if="department === 'erke' && bus_role === 'dietician'"
        class="float-right"
        :has-server="ifHasServer('recipe_service')"
      />
      <!-- 宝宝生长数据 -->
      <growth-data
        v-if="department === 'erke'"
        class="float-right"
        :baby-id="activedBabyId"
      />
    </div>
  </div>
</template>
<script>
import TransferDiagnose from './components/TransferDiagnose'

import HealthManage from './components/HealthManage'
import HealthFollow from './components/HealthFollow'

import MsgHistory from './components/MsgHistory'
import Recipes from './components/Recipes'
import GrowthData from './components/GrowthData'

import { ifHasServer } from '@/utils/chat/index'
import { mapGetters } from 'vuex'

export default {
  props: {},
  components: {
    'transfer-diagnose': TransferDiagnose,
    MsgHistory,
    HealthManage,
    HealthFollow,
    Recipes,
    GrowthData
  },
  data() {
    return {
      department: ''
    }
  },
  computed: {
    ...mapGetters([
      'activedBabyId',
      'activedBaby',
      'role',
      'bus_role',
      'userInfo'
    ])
  },
  created() {
    this.getDeprtment()
  },
  watch: {
    activedBaby: {
      handler() {
        this.getDeprtment()
      },
      deep: true
    }
  },
  methods: {
    ifHasServer,
    getDeprtment() {
      this.department =
        this.activedBaby && this.activedBaby.family_member
          ? this.activedBaby.family_member.type
          : ''
      // console.log(this.activedBaby, 'department====', this.department)
    }
  }
}
</script>
<style lang="scss" scoped>
div.float-left,
div.float-right {
  margin-right: 8px;
  line-height: 30px;
  text-align: center;
  color: #333;
  cursor: pointer;
  &:first-child {
    color: #000;
  }
}
div.float-left {
  margin-right: 0;
  margin-left: 8px;
}
</style>
