<template>
  <div class="send-image">
    <el-tooltip placement="top">
      <div slot="content">
        <p v-if="!hasServer">该用户套餐不包含此服务</p>
        <div v-else>
          <p v-if="uploading" :loading="uploading">
            <i class="el-icon-loading" /> 上传中
          </p>
          <p v-else>图片</p>
        </div>
      </div>
      <!-- 有咨询服务，且按钮状态可用才 disabled:false -->
      <el-upload
        :disabled="!hasServer || submitBtnStatusDisabled || uploading"
        :loading="uploading"
        class="avatar-uploader"
        action=""
        :on-error="handleError"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :http-request="uploadFunc"
        accept="image/gif, image/*"
        :before-upload="beforeAvatarUpload"
      >
        <svg-icon
          icon-class="photo"
          :class="!hasServer ? 'notAllow' : ''"
          class="color-black"
        />
      </el-upload>
    </el-tooltip>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { handleMessageSend } from '@/api/message'
import { upload } from '@/api/upload'
export default {
  name: 'SendCustom',
  props: {
    hasServer: {
      type: Boolean,
      default: false
    },
    submitBtnStatusDisabled: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      uploading: false
    }
  },
  computed: {
    ...mapGetters(['lastAsk'])
  },
  methods: {
    beforeAvatarUpload(file) {
      const isJPG =
        ['image/png', 'image/jpeg', 'image/jpg', 'image/gif'].indexOf(
          file.type
        ) >= 0
      const isLt3M = file.size / 1024 / 1024 < 3

      if (!isJPG) {
        this.$message.error('图片格式不正确!')
      }
      if (!isLt3M) {
        this.$message.error('上传图片大小不能超过 3MB!')
      }
      return isJPG && isLt3M
    },
    // 上传图
    async uploadFunc(option) {
      this.uploading = true
      const data = await upload(option.file, progress => {})
      if (data.url) {
        this.sendImageMsg(data)
      }
    },
    handleAvatarSuccess(res, file) {
      this.uploading = false
    },
    handleError(res, file) {
      this.uploading = false
    },
    // 发送图片消息
    sendImageMsg(res) {
      const meta = res.meta
      if (!this.lastAsk._id) return
      const params = {
        fd_ask: this.lastAsk._id, // 咨询id,
        message_body: {
          // 消息体
          type: 'image',
          url: res.url,
          width: meta.ImageWidth.value,
          height: meta.ImageHeight.value
        }
      }
      handleMessageSend(params)
        .then(res => {
          if (res.data.result) {
            this.$store.dispatch('pushCurrentIMInfoMessages', res.data.message)
            this.$store.dispatch('setBottomBtn', false, { root: true }) // 隐藏直达最新消息按钮
          } else {
            this.$message.error(res.data.message)
          }
        })
        .catch(err => {
          console.error('发送图片：', err)
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.color-black {
  color: #000;
}
.send-image {
  position: relative;
}
</style>
