<template>
  <div class="fast_reply">
    <el-tooltip content="快捷回复" placement="top">
      <svg-icon
        icon-class="kjhuifu"
        :class="!replyBoxFlag ? 'color-black' : 'color-blue'"
        @click.stop="showReplyBox"
      />
    </el-tooltip>
    <div
      v-show="replyBoxFlag"
      class="reply_box clearfix"
      @click.stop="showReplyBox"
    >
      <div class="reply_box_warp">
        <scroll-box :box-height="126">
          <div class="reply_list">
            <div
              v-for="item in replyList"
              :key="item._id"
              class="reply_list_item"
            >
              <span
                class="reply_value"
                @click.stop="ClickFastReply(item.content)"
              >
                {{ item.content }}
              </span>
              <span>
                <el-tooltip
                  class="item"
                  effect="dark"
                  content="编辑"
                  placement="top"
                >
                  <svg-icon
                    icon-class="bj"
                    class="color-blue edit_btn showBtn"
                    @click.stop="editReplyContent(item)"
                  />
                </el-tooltip>
                <el-tooltip
                  class="item"
                  effect="dark"
                  content="删除"
                  placement="top"
                >
                  <svg-icon
                    icon-class="sc"
                    class="color-blue del_btn showBtn"
                    @click.stop="delReplyContent(item)"
                  />
                </el-tooltip>
              </span>
            </div>
          </div>
        </scroll-box>
      </div>
      <div class="border-bottom" />
      <div class="reply_input">
        <el-row :gutter="24">
          <el-col :span="17">
            <el-input
              v-model="fastReplyContent"
              type="textarea"
              class="not_set_padding"
              :autosize="{ minRows: 1, maxRows: 1 }"
              size="mini"
              placeholder="请输入快捷回复内容"
            />
          </el-col>
          <el-col :span="6" class="col-btn">
            <el-button
              :type="fastReplyContent ? 'primary' : 'info'"
              size="mini"
              :disabled="fastReplyContent ? false : true"
              @click.stop="addFastReply"
              >设为回复内容</el-button
            >
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
  <!-- 回复设置 -->
</template>
<script>
import ScrollBox from '@/components/ScrollBox'
import { mapGetters } from 'vuex'
import {
  addFastReply,
  editFastReply,
  delFastReply,
  getFastReply
} from '@/api/diagnose'
export default {
  components: {
    ScrollBox
  },
  data() {
    return {
      editId: '',
      replyList: [],
      fastReplyContent: ''
    }
  },
  computed: {
    ...mapGetters(['replyBoxFlag', 'userInfo'])
  },
  watch: {
    replyBoxFlag(val) {}
  },
  created() {
    this.getFastReply()
  },
  methods: {
    getFastReply() {
      getFastReply().then(res => {
        this.replyList = res.data
      })
    },
    showReplyBox() {
      this.$store.dispatch('setReplyBoxFlag', true)
    },
    ClickFastReply(val) {
      this.$emit('ClickFastReply', val)
    },
    addFastReply() {
      const params = {
        content: this.fastReplyContent
      }
      if (!this.editId) {
        addFastReply(params).then(res => {
          if (res) {
            this.getFastReply()
            this.editId = ''
            this.fastReplyContent = ''
            const operatName = '设为回复内容'
            this.$message({
              type: res.data.result ? 'success' : 'error',
              message: res.data.result
                ? operatName + '成功'
                : operatName + '失败'
            })
          }
        })
      } else {
        editFastReply(this.editId, params).then(res => {
          if (res) {
            this.getFastReply()
            this.fastReplyContent = ''
            this.editId = ''
            const operatName = '设为回复内容'
            this.$message({
              type: res.data.result ? 'success' : 'error',
              message: res.data.result
                ? operatName + '成功'
                : operatName + '失败'
            })
          }
        })
      }
    },
    editReplyContent(item) {
      this.fastReplyContent = item.content
      this.editId = item._id
    },
    delReplyContent(item) {
      delFastReply(item._id).then(res => {
        if (res) {
          this.getFastReply()
          const operatName = '删除'
          this.$message({
            type: res.data.result ? 'success' : 'error',
            message: res.data.result ? operatName + '成功' : operatName + '失败'
          })
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.fast_reply {
  position: relative;
  .color-black {
    position: relative;
    top: 1px;
    font-size: 16px;
    color: #000;
    &:hover {
      color: #0085ff;
    }
  }
  .color-blue {
    position: relative;
    top: 1px;
    font-size: 16px;
    color: #0085ff;
  }
  .edit_btn {
    position: relative;
    top: -8px;
    margin-right: 5px;
    margin-left: 5px;
    font-size: 17px;
  }
  .del_btn {
    position: relative;
    top: -8px;
    font-size: 17px;
  }
  .reply_box {
    position: absolute;
    bottom: 35px;
    left: -32px;
    width: 330px;
    height: 178px;
    background: #fff;
    border-radius: 4px 4px 4px 0 0 0 4px;
    z-index: 10;
    box-shadow: 0 0 10px 0 rgba(153, 153, 153, 0.5);
  }
  .showBtn {
    display: none;
  }
  .reply_box_warp {
    position: relative;
    width: 100%;
    height: 70%;
    z-index: 300;
    .reply_list {
      padding: 0 10px;
      .reply_list_item {
        height: 30px;
        line-height: 30px;
        text-align: left;
        &:hover {
          color: #0085ff;
          background: #ecf4ff;
          border-radius: 4px;
          .reply_value {
            color: #0085ff;
          }
          .showBtn {
            display: inline-block;
            // z-index: 1;
          }
        }
      }
      .reply_value {
        display: inline-block;
        overflow: hidden;
        width: 80%;
        height: 30px;
        // border:1px solid red;
        line-height: 30px;
        font-size: 12px;
        font-family: PingFangSC-Regular;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #17233d;
        letter-spacing: 0;
      }
    }
  }
  .border-bottom {
    border-bottom: 1px solid #e8eaec;
  }
  .reply_input {
    padding: 5px 10px;
    width: 100%;
    height: 20%;
  }
  .col-btn {
    padding-right: 0 !important;
    padding-left: 0 !important;
    .el-button {
      padding: 9px 4px;
    }
  }
  .reply_box::before {
    position: absolute;
    bottom: -13px;
    left: 33px;
    display: block;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    content: '';
    border-top: 7px solid #fff;
  }
}
</style>
