<template>
  <div class="userinfo-wrap">
    <div class="user-box-title">用户信息</div>
    <div v-if="showUserInfo" class="user-and-baby-card">
      <scroll-box :box-height="815">
        <user class="user-box" :user="user"> </user>
        <div class="baby-wrap">
          <div class="baby-box" v-if="familyMermberList.length">
            <baby
              v-for="item in familyMermberList"
              :key="item._id"
              :babyDetail="item"
            ></baby>
          </div>
        </div>
      </scroll-box>
    </div>
  </div>
</template>

<script>
import ScrollBox from '@/components/ScrollBox'
import { mapGetters } from 'vuex'
import baby from './components/baby.vue'
import user from './components/user.vue'

export default {
  name: 'UserInfo',

  data() {
    return {
      showUserInfo: false,
      user: {},
      babyDetail: {},
      familyMermberList: []
    }
  },
  components: {
    ScrollBox,
    user,
    baby
  },
  computed: {
    ...mapGetters(['currentCustomer'])
  },
  watch: {
    currentCustomer: {
      handler: function(val) {
        this.userInfoFormat(val)
      },
      deep: true
    }
  },
  created() {},
  methods: {
    userInfoFormat(val) {
      const user = val.user
      if (!user) {
        this.showUserInfo = false
        return
      }
      this.user = {
        _id: user._id,
        user_id: user.user_id,
        remark: user.remark,
        name: user.name,
        nickname: user.nick_name,
        avatar: user.avatar
      }
      this.familyMermberList = val.family_member_list
      this.showUserInfo = true
    }
  }
}
</script>
<style lang="scss" scope>
.userinfo-wrap {
  border-left: 1px solid #e8eaec;
}
.user-box-title {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #0085ff;
  border-bottom: 2px solid #e8eaec;
  margin-bottom: 14px;
}
.user-and-baby-card {
  .user-box {
    padding: 2px 6px;
  }
}
span.span-val {
  color: #747d8f;
  // color: #999;
}
</style>
