<template>
  <div class="SendImage">
    <el-tooltip
      :content="!hasServer ? '该用户套餐不包含此服务' : '新建定制食谱任务'"
      placement="top"
    >
      <svg-icon
        icon-class="xjdzshipu"
        :class="!hasServer ? 'notAllow' : ''"
        @click="openDrawer"
      />
    </el-tooltip>
    <el-drawer
      title="新建定制食谱任务"
      size="25%"
      :modal="false"
      :visible.sync="drawer"
      direction="ltr"
      class="drawer-content"
      @opened="resetForm"
    >
      <div class="drawer-content">
        <el-form ref="ruleForm" :model="ruleForm" label-width="120px">
          <el-form-item label="当前用户：">
            <el-input v-model="customerName" disabled />
          </el-form-item>
          <el-form-item label="宝宝：">
            <el-input v-model="activedBaby.name" disabled />
          </el-form-item>
          <el-form-item label="月龄：">
            <el-input v-model="activedBaby.age_of_month" disabled />
          </el-form-item>
          <el-form-item
            label="模板名称："
            prop="recipe_template"
            :rules="[
              { required: true, message: '请选择服务类型', trigger: 'change' }
            ]"
          >
            <el-select
              v-model="ruleForm.recipe_template"
              @change="getRecipesTitle"
            >
              <el-option
                v-for="item in templateDic"
                :key="item._id"
                :label="item.title"
                :value="item._id"
              />
            </el-select>
          </el-form-item>
          <el-form-item
            label="食谱名称："
            prop="title"
            :rules="[
              { required: true, message: '请填写食谱名称', trigger: 'blur' }
            ]"
          >
            <div>
              <el-input
                v-model="ruleForm.title"
                maxlength="50"
                show-word-limit
              />
            </div>
          </el-form-item>
          <el-form-item label="备注：" prop="desc">
            <el-input
              v-model="ruleForm.desc"
              type="textarea"
              maxlength="100"
              show-word-limit
              rows="5"
              resize="none"
            />
          </el-form-item>
          <el-form-item
            prop="executeTime"
            :rules="[
              { required: true, message: '请选择任务执行日期', trigger: 'blur' }
            ]"
          >
            <span slot="label">
              任务执行日期
            </span>
            <div>
              <el-date-picker
                v-model="ruleForm.executeTime"
                type="date"
                placeholder="请选择任务执行日期"
                :picker-options="pickerOptions"
                style="width: 100%;"
              />
            </div>
          </el-form-item>
          <el-form-item class="form-btn">
            <el-button type="primary" @click="createTask">确认创建</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import { createTask } from '@/api/recipes'
export default {
  name: 'SendCustom',
  props: {
    hasServer: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 1000 * 60 * 60 * 24
        }
      },
      uploading: false,
      drawer: false,
      ruleForm: {
        title: '',
        desc: '',
        executeTime: '',
        recipe_template: ''
      }
    }
  },
  computed: {
    ...mapGetters([
      'activedBaby',
      'activedBabyId',
      'activedUserInfo',
      'templateDic'
    ]),
    customerName() {
      const currentuser = this.activedUserInfo
      return currentuser.name
    }
  },
  mounted() {
    // 获取食谱模板下拉列表
    this.getTemplateDic()
  },
  methods: {
    ...mapActions({
      getTemplateDic: 'recipes/getTemplateDic'
    }),
    getRecipesTitle(val) {
      this.templateDic.forEach(item => {
        if (item._id === val) {
          this.ruleForm.title = item.title
        }
      })
    },
    resetForm() {
      this.$refs.ruleForm.resetFields()
    },
    openDrawer() {
      if (!this.hasServer) {
        return
      }
      this.drawer = true
    },
    closeDrawer() {
      setTimeout(() => {
        this.drawer = false
      }, 1000)
    },
    createTask() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          const babyId = this.activedBabyId
          createTask(babyId, this.ruleForm).then(res => {
            if (res.code === 20000) {
              this.$message.success('任务创建成功')
              this.closeDrawer()
            } else {
              this.$message.warning('任务创建失败')
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.drawer-content {
  padding-right: 20px;
  .drawer-item {
    display: flex;
    margin-left: 25px;
    line-height: 50px;
    font-size: 14px;
    color: #17233d;
    .label {
      width: 150px;
      text-align: right;
      flex: 1;
    }
    .name {
      margin-left: 20px;
      flex: 4;
    }
  }
  .button-container {
    text-align: right;
  }
  .el-input {
    width: 200px !important;
  }
  .form-btn {
    /deep/.el-form-item__content {
      margin-left: 0 !important;
      text-align: center;
      .el-button {
        padding: 0 10px;
        height: 32px;
        line-height: 32px;
      }
    }
  }
  /deep/.el-drawer__header {
    font-weight: 600;
    text-align: center;
    color: #17233d;
  }
}
</style>
