<template>
  <div
    :class="['session-container', { active: classIsActive(session) }]"
    @click="handleCustomer(session)"
  >
    <el-row :gutter="24" type="flex" justify="space-between">
      <!--信息-->
      <el-col :span="6">
        <el-badge
          v-if="session.session_id === sessionId && unReadCount"
          :value="unReadCount"
        >
          <el-avatar
            size="large"
            :src="session.user.avatar"
            :key="session.user._id"
          />
        </el-badge>
        <el-badge
          v-else-if="session.un_read_count > 0"
          :value="session.un_read_count"
        >
          <el-avatar
            size="large"
            :src="session.user.avatar"
            :key="session.user._id"
          />
        </el-badge>
        <el-avatar v-else size="large" :src="session.user.avatar" />
      </el-col>
      <el-col :span="18" style="line-height:18px;font-size: 10px;color: #999;">
        <!--客户姓名-->
        <div>
          <div
            v-if="!remarkEditable"
            :title="edit && role === 'hm' ? '双击修改备注名' : ''"
            style="display:inline-block;font-size: 14px;text-align:left;color: #17233D;"
            @dblclick="role === 'hm' ? editRemark(session) : () => {}"
          >
            <span v-if="session.user.name">{{
              session.user.name | ellipsis(8)
            }}</span>
            <span v-else>{{ session.user.name | ellipsis(5) }}</span>
          </div>
          <input
            v-else
            v-model="remarkName"
            v-myfocus
            style="width: 100px"
            @blur="onRemarkBlur(session, remarkName)"
          />
          <span class="float-right">
            &nbsp;&nbsp;{{ getTimeByStatus(session) }}
          </span>
          <!--宝宝命名-->
          <div class="float-right" v-if="chatListType === 'messageQueue'">
            <span class="baby-name" v-if="session.family_member">
              {{ session.family_member.name | ellipsis(8) }}
            </span>
          </div>
        </div>
        <div class="last_msg_and_icon" style="display:inline-block;">
          <!--图标-->
          <span v-if="session.last_message">
            {{ session.last_message.content | ellipsis(5) }}
          </span>
          <!--待回复或回复中状态图标-->
          <span
            v-if="
              session.last_ask &&
                session.last_ask.status !== 'ask_end' &&
                session.last_ask.round.reply_status &&
                session.last_ask.round.reply_status !== 'stop_reply'
            "
          >
            <el-tooltip
              effect="dark"
              content="本回合还未回复完毕"
              placement="top"
            >
              <svg-icon icon-class="waitreply" />
            </el-tooltip>
          </span>
          <span
            v-if="
              session.last_ask &&
                session.last_ask.status !== 'ask_end' &&
                session.last_ask.has_transfer
            "
          >
            <!--电话、图文图标-->
            <el-tooltip
              effect="dark"
              :content="UserListTransferStatusIcon(session).content"
              placement="top"
            >
              <i
                class="iconfont"
                style="margin:0 0 0 5px;font-size:11px;"
                :class="UserListTransferStatusIcon(session).iconType"
                :style="{
                  'background-color': UserListTransferStatusIcon(session)
                    .dotColor,
                  'border-color': UserListTransferStatusIcon(session)
                    .borderColor,
                  color: UserListTransferStatusIcon(session).borderColor
                }"
              />
            </el-tooltip>
          </span>
          <!-- 代班服务 -->
          <el-tooltip effect="dark" content="代班服务中" placement="top">
            <svg-icon
              v-show="session.is_replace"
              style="margin-left:5px;"
              icon-class="replace"
            />
          </el-tooltip>
          <!-- 超时警告或提醒 -->
          <el-tooltip
            v-if="session.chat_timer && session.chat_timer.show_timer"
            :content="waitStyle(session).text + formatWaitTime"
            placement="top"
          >
            <svg-icon
              style="margin-left:5px;"
              :icon-class="waitStyle(session).iconType"
              @mouseleave="mouseleaveTip"
              @mouseenter="mouseenterTip"
            />
          </el-tooltip>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { waitTime, waitStyle } from '@/utils/moment'
import { UserListTransferStatusIcon } from '@/filters'
import { chatUserRemark } from '@/api/userInfo'
export default {
  name: 'SessionCard',
  props: {
    edit: {
      type: Boolean,
      default: false
    },
    session: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      remarkEditable: false,
      remarkName: '',
      formatWaitTime: '00:00:00',
      waitTimer: null
    }
  },
  computed: {
    ...mapGetters(['unReadCount', 'sessionId', 'chatListType', 'role'])
  },
  methods: {
    UserListTransferStatusIcon,
    mouseleaveTip() {
      clearInterval(this.waitTimer)
    },
    mouseenterTip() {
      this.setTimer()
    },
    classIsActive(val) {
      return val.session_id === this.sessionId
    },
    waitStyle,
    handleCustomer(customer) {
      const obj = {
        session_id: customer.session_id,
        userId: customer.user._id,
        babyId: customer.family_member ? customer.family_member._id : ''
      }
      this.$store.dispatch('setCustomerInfo', obj)
    },
    setTimer() {
      if (this.session.chat_timer && this.session.chat_timer.start_at) {
        this.formatWaitTime = this.waitTime(this.session.chat_timer.start_at)
        this.waitTimer = setInterval(() => {
          this.formatWaitTime = this.waitTime(this.session.chat_timer.start_at)
        }, 1000)
      }
    },
    updateRemarkNameFun(customer, remarkName) {
      // 修改用户备注名接口 模拟接口
      const params = {
        remark: '',
        remark_name: remarkName ? remarkName : ''
      }
      chatUserRemark(customer.user._id, params).then(res => {
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.message
        })
        if (res.data.result) {
          // 请求成功，刷新会话列表
          this.$store.dispatch('chat/getCustomerTree', { root: true }) // 刷新聊天窗口顶部客户信息
          this.$store.dispatch(
            'chat/getActivedUserInfo',
            { id: customer.user._id },
            { root: true }
          )
        }
      })
    },
    onRemarkBlur(session, remarkName) {
      this.remarkEditable = false
      this.updateRemarkNameFun(session, remarkName)
    },
    editRemark(session) {
      if (this.edit) {
        this.remarkEditable = true
        this.remarkName = session.user.name
      }
    },
    getTimeByStatus(session) {
      let timeChangeVal = ''
      if (session.last_message && session.last_message.created_at) {
        timeChangeVal = this.timeByIsBefore(
          session.last_message.created_at,
          'm-d',
          'h:m'
        )
        return timeChangeVal
      } else {
        return timeChangeVal
      }
    },
    waitTime
  }
}
</script>

<style scoped lang="scss">
.session-container {
  position: relative;
  padding: 10px 14px 0 8px;
  height: 60px;
  cursor: pointer;
  &:hover {
    background-color: #f6f7fb;
  }
  .baby-name {
    padding: 2px 5px;
    line-height: 14px;
    font-size: 9px;
    color: #0085ff;
    background-color: #d9ecff;
    border-radius: 12px;
  }
  .last_msg_and_icon {
    margin-top: 4px;
    line-height: 16px;
  }
  .chat_time_icon {
    float: right;
    margin-left: 4px;
  }
}
.active {
  background-color: #f6f7fb;
}
</style>
