<template>
  <div ref="refmsgCard" class="msg-card">
    <p
      v-show="searchType !== 'imgAndVideo' && msgList.length !== 0"
      class="tip"
      v-html="tip"
    />
    <p
      v-show="
        (keyword && msgList.length === 0) ||
          (formInline.consultationTime &&
            formInline.consultationTime.length > 0 &&
            msgList.length === 0)
      "
    >
      没有找到相关记录
    </p>
    <div v-if="msgList.length > 0" class="infinite-list-wrapper">
      <ul
        v-if="searchType !== 'imgAndVideo'"
        v-infinite-scroll="loadMore"
        infinite-scroll-immediate="disabled"
        class="list"
      >
        <li v-for="(item, index) in msgList" :key="index">
          <el-row :gutter="24" :class="searchType">
            <el-col :span="2">
              <img class="head-Img" :src="item.imgUrl" alt="" />
            </el-col>
            <el-col :span="22" :class="searchType + '-right'">
              <p class="color-999 align-left">
                <span>{{ item.name }}</span>
                <span class="fontsize-10 margin-left-20">{{
                  item.time ? formatTime(item.time) : '-'
                }}</span>
              </p>
              <!-- 不同类型的消息显示 -->
              <msgType
                :item="item"
                :keyword="keyword"
                :msgType="item.content.type"
                @goMsg="goMessageDetail"
              />
            </el-col>
          </el-row>
        </li>
      </ul>
      <ul
        v-if="searchType === 'imgAndVideo' && imageArr.length > 0"
        v-infinite-scroll="loadMore"
        infinite-scroll-immediate="disabled"
        class="list"
      >
        <li
          v-for="(item, index) in imageArr"
          :key="index"
          style="margin:0 10px;"
        >
          <div :class="searchType" class="clearfix">
            <div :class="searchType + '-right'">
              <p class="color-999 align-left" style="margin-left:10px;">
                {{ item[0] ? item[0].time : '-' }}
              </p>
              <span
                v-for="(child, ind) in item"
                :key="child + index + ind"
                class="image-box"
              >
                <img
                  v-if="child.content.type === 'image'"
                  :span="5"
                  :src="child.content.url"
                  alt=""
                  @click="goMessageDetail(child)"
                />
                <div
                  v-if="child.content.type === 'video'"
                  :span="5"
                  class="cardVideoContent"
                  @click="goMessageDetail(child)"
                >
                  <img
                    :src="
                      child.content.url +
                        '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                    "
                    :data-src="
                      child.content.url +
                        '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
                    "
                    alt=""
                  />
                  <i class="el-icon-video-play" />
                </div>
              </span>
            </div>
          </div>
        </li>
      </ul>
      <p v-if="loading">加载中...</p>
      <p v-if="noMore">没有更多了</p>
    </div>
  </div>
</template>
<script>
import { brightenKeyword } from '@/utils/index.js'
import { mapGetters } from 'vuex'
import msgType from './msgType'
export default {
  components: {
    msgType
  },
  props: {
    formInline: {
      type: Object,
      default: () => {}
    },
    keyword: {
      type: String,
      default: ''
    },
    searchType: {
      type: String,
      default: 'all'
    },
    msgList: {
      type: Array,
      default: () => []
    },
    pageTotal: {
      type: Number,
      default: 0
    },
    page: {
      type: Number,
      default: 1
    }
  },

  data() {
    return {
      // 加载状态
      loading: false,
      pagesize: 10, // 当前页数
      pageSizes: 0, // 总页数
      noMore: '',
      tip: '',
      imageArr: []
    }
  },
  computed: {
    ...mapGetters(['activedBaby', 'getCurrentIMInfoMessages']),
    disabled() {
      return this.loading || this.noMore
    }
  },
  watch: {
    msgList(val) {
      this.getTipFun()
      if (this.searchType === 'imgAndVideo') {
        this.getImageArr()
      }
    },
    pageTotal() {
      this.pageSizes =
        this.pageTotal % this.pagesize === 0
          ? this.pageTotal / this.pagesize
          : this.pageTotal / this.pagesize + 1
    }
  },
  created() {
    this.getTipFun()
  },
  methods: {
    async goMessageDetail(msgObj) {
      this.$emit('toMessage', msgObj)
      await this.$store.dispatch('goMessageDetail', msgObj.id)
      this.$store.dispatch('setBottomBtn', true, { root: true }) // 显示直达最新消息按钮
    },
    getImageArr() {
      this.imageArr.length = 0
      this.imageArr = Object.values(
        this.msgList.reduce((res, item) => {
          res[item.time] ? res[item.time].push(item) : (res[item.time] = [item])
          return res
        }, {})
      )
    },
    brightenKeyword,
    getTipFun() {
      const title = this.keyword
        ? this.pageTotal + '条与“' + this.keyword + '”相关的内容'
        : ''
      this.tip = this.brightenKeyword(title, this.keyword)
    },
    // 加载更多
    loadMore() {
      // 是否当前page不是最后一页
      if (this.page <= this.pageSizes) {
        this.loading = true
        // 模拟分页查询
        // 页码+1
        this.$emit('page')
        this.$emit('getMsgListMore')
        this.loading = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#user-table {
  width: 100%;
  height: 368px;
}
.infinite-list-wrapper {
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 378px;
}
.cardVideoContent {
  position: relative;
  max-width: 350px;
  img {
    max-width: 290px;
    max-height: 260px;
  }
  i.el-icon-video-play {
    position: absolute;
    bottom: 40%;
    left: 31%;
    font-size: 35px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}

.msg-card {
  padding-bottom: 100px;
  font-family: PingFangSC-Regular;
  .tip {
    font-size: 14px;
    text-align: center;
    color: #17233d;
    letter-spacing: 0;
  }
  .head-Img {
    width: 40px;
    height: 40px;
    border: 1px solid #eee;
    border-radius: 50%;
  }
  .el-row.all {
    padding: 10px 15px;
    &:first-child {
      border-top: 1px solid #e8eaec !important;
    }
    &:nth-child(odd) {
      background: #fff;
      box-shadow: inset 0 1px 0 0 #e8eaec;
    }
    &:nth-child(even) {
      background: #fff;
      box-shadow: inset 0 1px 0 0 #e8eaec;
    }
    &:hover {
      background: #f6f7fb;
    }
  }
  .el-row.imgAndVideo {
    margin: 0 !important;
    .imgAndVideo-right {
      margin: 0 auto;
      width: 100%;
      .margin-left-20 {
        margin-top: 5px;
        margin-left: 0;
        font-size: 14px;
        color: #576277;
      }
    }
    img {
      float: left;
      margin-right: 10px;
      width: 66px;
      height: 66px;
      border: 1px solid #eee;
      margin-bottom: 10px;
    }
  }
  .image-box {
    position: relative;
    float: left;
    padding: 0 10px;
    text-align: left;
    img {
      width: 100px;
      height: 100px;
    }
  }
  .el-row.imgAndVideo::after {
    display: block;
    clear: both;
    content: '';
  }
  .content {
    line-height: 16px;
    font-size: 12px;
    /* stylelint-disable-next-line no-descending-specificity */
    color: #576277;
  }
}
</style>
