import request from '@/utils/request'
export function createGrowthInfo(data) {
  return request({
    url: '/baby_growth_data/create',
    method: 'post',
    data: data
  })
}
// 获取宝宝生长数据列表
export function getGrowthList(id) {
  return request({
    url: `/baby_growth_data/list/${id}`,
    method: 'get'
  })
}

// 删除宝宝生长数据
export function deleteGrowthInfo(id) {
  return request({
    url: `/baby_growth_data/destroy/${id}`,
    method: 'delete'
  })
}
// 编辑宝宝生长数据
export function updateGrowthInfo(id, data) {
  return request({
    url: `/baby_growth_data/edit/${id}`,
    method: 'put',
    data: data
  })
}
