<template>
  <div class="time-line-wrap">
    <div class="case-timeline-title">当前咨询轨迹</div>
    <scroll-box :box-height="808" class="time-line-box">
      <div
        v-for="(timePot, index) in timeLineInfo"
        :key="timePot.created_at + index"
        class="case-timeline-card"
      >
        <div class="time-line">
          <div>
            <div
              class="dot"
              :style="{
                'background-color': switchDotStyle(timePot).bgColor,
                border: switchDotStyle(timePot).border,
                color: switchDotStyle(timePot).color
                  ? switchDotStyle(timePot).color
                  : '#fff'
              }"
            >
              <i class="iconfont" :class="switchDotStyle(timePot).iconType" />
              <svg-icon
                v-show="timePot.is_replace"
                class="cover_icon"
                icon-class="yxdaiban1"
              />
            </div>
          </div>
          <div class="line" />
          <div v-if="answerWay(timePot.type, '')">
            <div class="card-title">
              <div>{{ timePot.user.name }}</div>
              <div>
                {{ answerWay(timePot.type, '') }}
              </div>
            </div>
            <div
              v-if="!cardEditable || index !== editIndex"
              class="card-nurse-show"
            >
              <div>状态：{{ resolveStatus(timePot.status, '') }}</div>
              <collapse-box
                :collapse-text="timePot.main_ask ? timePot.main_ask : ''"
                :title="'主诉'"
              />
              <collapse-box
                :collapse-text="timePot.suggestion"
                :title="
                  timePot.suggestion_type
                    ? suggestType(timePot.suggestion_type)
                    : '叮嘱'
                "
              />
              <div class="card-edit">
                <el-button
                  type="text"
                  :disabled="
                    getEditDisabled(timePot).editStatus ||
                      (getReplaceStatus.replace &&
                        getReplaceStatus.replace.status === 'replace_by_other')
                  "
                  @click="showEdit(index)"
                >
                  编辑
                </el-button>
              </div>
            </div>
            <div
              v-else-if="cardEditable && index === editIndex"
              class="card-nurse-edit"
            >
              <div>
                <div>状态</div>
                <div>
                  <el-select
                    v-model="suggestionStatus"
                    placeholder="请选择"
                    size="mini"
                  >
                    <el-option
                      v-for="item in statusOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    />
                  </el-select>
                </div>
              </div>
              <div>
                <div>主诉</div>
                <div>
                  <el-input
                    v-model="main_ask"
                    size="mini"
                    type="textarea"
                    :rows="3"
                    placeholder="请输入内容"
                    maxlength="100"
                    show-word-limit
                  />
                </div>
              </div>
              <div>
                <div style="margin-top:10px;width:85px;">
                  <el-select
                    v-model="suggestionType"
                    size="mini"
                    placeholder="请选择"
                  >
                    <el-option
                      v-for="item in suggestOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    />
                  </el-select>
                </div>
                <div>
                  <el-input
                    v-model="suggestion_input"
                    size="mini"
                    type="textarea"
                    :rows="3"
                    :placeholder="
                      '请输入' + suggestType(timePot.suggestion_type)
                    "
                    maxlength="100"
                    show-word-limit
                  />
                </div>
                <div class="clearfix">
                  <!-- 当自动结束咨询时，可以点击发送按钮进行发送 -->
                  <el-button
                    v-if="showSend"
                    class="save"
                    type="primary"
                    style="margin-left:10px;"
                    :disabled="getEditDisabled(timePot).editStatus"
                    size="mini"
                    @click="handleSave(timePot, index, 'send')"
                    >发送</el-button
                  >
                  <el-button
                    class="save"
                    type="primary"
                    :disabled="getEditDisabled(timePot).editStatus"
                    size="mini"
                    @click="handleSave(timePot, index, 'save')"
                    >保存</el-button
                  >
                </div>
              </div>
            </div>
            <div class="card-time">
              {{ new Date(timePot.created_at) | formatTime() }}
            </div>
          </div>
          <div v-else-if="timePot.type === 'over_time_send_suggestion'">
            <div class="card-title">
              <div>发送叮嘱/建议</div>
            </div>
            <div class="card-nurse-show">
              <collapse-box
                :collapse-text="timePot.suggestion"
                :title="
                  timePot.suggestion_type
                    ? suggestType(timePot.suggestion_type)
                    : '叮嘱'
                "
              />
            </div>
            <div class="card-time">
              {{ new Date(timePot.created_at) | formatTime() }}
            </div>
          </div>
          <div
            v-else-if="
              timePot.type === 'user_transfer' ||
                timePot.type === 'round_apply_transfer'
            "
          >
            <div class="card-title">
              <div>用户</div>
              <div>{{ timePot.title }}</div>
            </div>
            <div v-if="timePot.user_desc" class="customer-info">
              留言内容：{{ timePot.user_desc }}
            </div>
            <div class="card-time">
              {{ new Date(timePot.created_at) | formatTime() }}
            </div>
          </div>
          <div v-else-if="timePot.type === 'user_ask'">
            <div class="card-title">
              <div>用户</div>
              <div>健康咨询</div>
            </div>
            <div class="customer-info">
              <div>症状：{{ timePot.ask_question_desc }}</div>
              <div>
                干预史：{{
                  timePot.interven_desc ? timePot.interven_desc : '无'
                }}
              </div>
              <div>
                资料照片及视频：
                <span v-if="timePot.ask_question_report.length === 0">无</span>
              </div>
              <el-row
                v-if="timePot.ask_question_report.length > 0"
                style="margin-top: 20px"
              >
                <el-col
                  v-for="(media, subIndex) in timePot.ask_question_report"
                  :key="'meida' + subIndex"
                  :span="8"
                >
                  <media-display-item
                    :url="media.url"
                    :media-type="media.type"
                    :url-list="timePot.ask_question_report"
                    :show-media-detail="showMediaDetail"
                  />
                </el-col>
              </el-row>
            </div>
            <div class="card-time">
              {{ new Date(timePot.created_at) | formatTime() }}
            </div>
          </div>
          <timeLineDom v-else :timePot="timePot"></timeLineDom>
        </div>
      </div>
    </scroll-box>
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUrlList"
    />
    <!-- 建议或叮嘱发送提示弹窗 -->
    <Dialog
      v-model="dialogVisible"
      :title="noSuggest ? '请填写建议或叮嘱' : '确认发送给用户的内容'"
      :showCancelBtn="true"
      :cancelText="noSuggest ? '返回填写' : '取消'"
      :hasConfirmBtn="!noSuggest"
      :btnText="'确认发送'"
      @btnHandle="confirmSend"
      :width="'460px'"
    >
      <!-- 填写建议或叮嘱的时候显示建议或叮嘱的内容 -->
      <template v-slot:content>
        <div v-if="!noSuggest" class="suggest">
          <p>
            {{ suggestType(suggestionParams.suggestion_type) }}：
            <span>{{ suggestionParams.suggestion }}</span>
          </p>
          <p style="line-height:20px;">
            建议部分的内容会反馈给用户，请谨慎填写，如需变更，请回到当前咨询轨迹区域进行编辑
          </p>
        </div>
        <!-- 没有填写的时候显示提示信息 -->
        <span v-if="noSuggest" style="line-height:20px;">{{
          dialogContent
        }}</span>
      </template>
    </Dialog>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import { answerWay, resolveStatus, suggestType } from '@/utils/dictionary'
import ScrollBox from '@/components/ScrollBox'
import MediaDisplayItem from './common/MediaDisplayItem'
import Monitor from '@/components/Monitor'
import { saveSuggestion } from '@/api/chat'
import CollapseBox from './common/CollapseBox'
import Dialog from '@/components/Dialog'
import { switchDotStyle } from '@/utils/chat'
import timeLineDom from './timeLineDom'
import _ from 'lodash'
export default {
  name: 'CaseTimeline',
  components: {
    ScrollBox,
    MediaDisplayItem,
    CollapseBox,
    Dialog,
    Monitor,
    timeLineDom
  },
  data() {
    return {
      dialogVisible: false,
      suggestionParams: {
        id: '',
        status: '',
        suggestion_type: '',
        suggestion: this.suggestion_input,
        main_ask: '',
        _id: '',
        save_type: 'save'
      },
      suggestionStatus: '',
      suggestionType: '',
      main_ask: '',
      babyIdChange: false,
      noSuggest: true,
      dialogContent:
        '请在当前咨询轨迹区域填写建议或叮嘱，该建议将作为此次咨询的指导方案提交给用户，请谨慎填写！',
      test: true,
      monitorInfo: {},
      curUrlList: [],
      myLastAsk: { has_transfer: false },
      cardEditable: false,
      editIndex: '',
      suggestOptions: this.suggestType('', 'arr'),
      suggestion_input: '',
      statusOptions: this.resolveStatus('', 'arr')
    }
  },
  computed: {
    ...mapGetters([
      'timeLineInfo',
      'activedBabyId',
      'activedBaby',
      'current_fdAsk',
      'userInfo',
      'role',
      'lastAsk',
      'getReplaceStatus',
      'showSend', //是否显示发送按钮
      'endedType', //当前轨迹的咨询是否已结束
      'manageAnswer',
      'currentTransferTextAnswer'
    ])
  },
  watch: {
    lastAsk: {
      handler: function(val, oldVal) {
        if (!oldVal || (oldVal._id && val._id !== oldVal._id)) {
          this.$store.dispatch('monitorFdAskChange', this.lastAsk._id)
        }
        this.myLastAsk = val ? val : ''
        this.babyIdChange = true
      },
      deep: true
    },
    // 当前宝宝信息发生改变的时候，刷新咨询轨迹
    activedBabyId: {
      handler: function(val, oldVal) {
        // 会话的宝宝改变时，可扩展区域默认显示宝宝资料
        if (val !== oldVal) {
          this.setActiveInfo('sharingNotes')
        }
        // 切换宝宝时，缓存当前对话的叮嘱或建议及是否是编辑状态和主诉信息等
        if (oldVal) {
          const params = {
            babyId: oldVal,
            sug: this.suggestion_input,
            mainAsk: this.main_ask,
            editIndex: this.editIndex,
            cardEditable: this.cardEditable,
            suggestionStatus: this.suggestionStatus,
            suggestionType: this.suggestionType
          }
          window.sessionStorage.setItem(
            'localSug' + oldVal,
            JSON.stringify(params)
          )
        }
        const localSug = window.sessionStorage.getItem('localSug' + val)
        const localSugJson = JSON.parse(localSug)
        if (localSugJson && localSugJson.babyId === this.activedBabyId) {
          this.suggestion_input = localSugJson.sug
          this.editIndex = localSugJson.editIndex
          this.cardEditable = localSugJson.cardEditable
          this.main_ask = localSugJson.mainAsk
          this.suggestionStatus = localSugJson.suggestionStatus
          this.suggestionType = localSugJson.suggestionType
        } else {
          this.suggestion_input = ''
          this.cardEditable = false
          this.editIndex = ''
          this.main_ask = ''
          this.suggestionStatus = 'resolved'
          this.suggestionType = 'suggest'
        }
      }
    },
    // 聊天界面滚动，fdAsk发生变化的时候，刷新咨询轨迹
    current_fdAsk: {
      handler: function(val) {
        if (!val) {
          this.setTimeLineInfo([])
          return
        }
        this.getTimeLineInfo(val)
        const localSug = window.sessionStorage.getItem(
          'localSug' + this.activedBabyId
        )
        const localSugJson = JSON.parse(localSug)
        if (this.babyIdChange && localSugJson) {
          this.cardEditable = localSugJson.cardEditable
          this.babyIdChange = false
        } else {
          this.cardEditable = false
        }
      }
    }
  },
  created() {
    this.myLastAsk = Object.assign({ has_transfer: false }, this.lastAsk)
  },
  methods: {
    answerWay,
    resolveStatus,
    suggestType,
    confirmSend() {
      this.saveSuggestionFun()
    },
    getEditDisabled(path) {
      const pathType = path.type
      let editStatus = true
      if (this.endedType !== 'none' && this.showSend) {
        // 会话已结束,且可以发送
        if (
          (pathType === 'voice_transfer_answer' && this.role === 'doctor') ||
          (pathType === 'text_transfer_answer' &&
            this.role === this.currentTransferTextAnswer.bus_role)
        ) {
          editStatus = false
        } else if (pathType === 'text_answer' && this.role === 'hm') {
          const haveTransfer = _.findIndex(this.timeLineInfo, function(o) {
            return (
              o.type == 'voice_transfer_answer' ||
              o.type === 'text_transfer_answer'
            )
          })
          // 不存在转诊的轨迹时
          if (haveTransfer < 0) {
            editStatus = false
          }
        }
      } else if (this.endedType === 'none') {
        // 会话未结束
        if (!this.myLastAsk.has_transfer && this.role === 'hm') {
          editStatus = false
        } else if (this.myLastAsk.has_transfer) {
          const lastAskTransferStatus = this.myLastAsk.transfer_status
          switch (lastAskTransferStatus) {
            case 'apply':
            case 'accept':
              if (pathType === 'text_answer' && this.role === 'hm') {
                editStatus = false
              }
              break
            case 'reject':
            case 'failed':
              if (this.role === 'hm') {
                editStatus = false
              }
              break
            case 'asking':
              if (
                pathType === 'voice_transfer_answer' &&
                this.role === this.myLastAsk.transfer_role
              ) {
                editStatus = false
              } else if (
                pathType === 'text_transfer_answer' &&
                this.role === this.myLastAsk.transfer_role
              ) {
                editStatus = false
              }
              break
            case 'start':
            case 'end':
              break
          }
        }
      }
      return {
        editStatus
      }
    },
    ...mapActions({
      getTimeLineInfo: 'chat/getTimeLineInfo'
    }),
    ...mapMutations({
      setTimeLineInfo: 'chat/SET_TIMELINEINFO',
      setActiveInfo: 'babyExtend/SET_ACTIVEINFO'
    }),
    showEdit(index) {
      this.cardEditable = true
      this.suggestion_input = this.timeLineInfo[index].suggestion
      this.main_ask = this.timeLineInfo[index].main_ask
      this.suggestionStatus = this.timeLineInfo[index].status
      this.suggestionType = this.timeLineInfo[index].suggestion_type
      this.editIndex = index
    },
    showMediaDetail(urlList, type, url) {
      if (type === 'image') {
        this.curUrlList = urlList
          .filter(item => item.type === 'image')
          .map(({ url }) => url)
      } else {
        this.curUrlList = []
      }
      this.monitorInfo = {
        type,
        meta: { url }
      }
      this.$refs.monitor.show()
    },
    handleSave(timePot, index, save_type) {
      const params = {
        id: this.myLastAsk._id,
        status: this.suggestionStatus,
        suggestion_type: this.suggestionType,
        suggestion: this.suggestion_input,
        main_ask: this.main_ask,
        _id: timePot._id,
        save_type: save_type
      }
      this.suggestionParams = params
      if (save_type === 'send') {
        this.noSuggest = this.suggestion_input ? false : true
        this.dialogVisible = true
        return
      }
      this.saveSuggestionFun()
    },
    saveSuggestionFun() {
      saveSuggestion(this.suggestionParams).then(res => {
        if (res.data.result === true) {
          this.dialogVisible = false
          this.cardEditable = false
          this.editIndex = ''
          this.suggestion_input = ''
          this.$message.success('保存成功')
          // 保存或发送成功后刷新咨询轨迹
          this.getTimeLineInfo(this.current_fdAsk)
        }
      })
    },
    switchDotStyle(timePot) {
      return switchDotStyle(timePot)
    }
  }
}
</script>

<style scoped lang="scss">
.time-line-wrap {
  border-right: 1px solid #e8eaec;
}
.case-timeline-title {
  padding: 12px 20px;
  font-size: 14px;
  text-align: center;
  color: #0085ff;
  border-bottom: 2px solid #e8eaec;
  margin-bottom: 20px;
}
.suggest {
  line-height: 22px;
}
.case-timeline-card {
  padding: 0 10px;
  .line:last-child {
    display: none;
  }
  &:last-child {
    .time-line {
      .line {
        display: none;
      }
    }
  }

  .time-line {
    position: relative;
    .dot {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 26px;
      height: 26px;
      // color: #fff;
      // border: 1px solid white;
      border-radius: 50%;
    }

    .line {
      position: absolute;
      top: 26px;
      bottom: 0;
      margin-left: 13px;
      width: 2px;
      background-color: #d8d8d8;
    }

    >>> .card-title {
      margin-top: -20px;
      margin-left: 31px;
      height: 22px;
      font-size: 14px;
      color: #17233d;
      div {
        display: inline-block;
        &:last-child {
          margin-left: 8px;
        }
      }
    }

    >>> .card-time {
      margin-left: 32px;
      padding-bottom: 20px;
      font-size: 12px;
      color: #999;
    }

    .card-nurse-show {
      position: relative;
      margin-left: 32px;
      line-height: 24px;
      font-size: 12px;
      color: #576277;
      .card-edit {
        position: absolute;
        top: -8px;
        right: 10px;
        color: #0085ff;
        cursor: pointer;
      }
    }

    .card-nurse-edit {
      margin-right: 20px;
      margin-left: 32px;
      line-height: 24px;
      font-size: 12px;
      color: #576277;
      margin-bottom: 15px;
      .save {
        float: right;
        margin: 5px 0;
        text-align: right;
      }
    }
    >>> .customer-info {
      position: relative;
      margin-left: 32px;
      line-height: 24px;
      font-size: 12px;
      color: #576277;
      word-break: break-all;
    }
  }
}
.el-button--text {
  color: #0085ff;
}
.cover_icon_box {
  position: absolute;
  width: 20px;
  height: 20px;
}
.cover_icon {
  position: absolute;
  top: 12px;
  left: 15px;
  font-size: 15px;
  border-radius: 50%;
}
</style>
